<template>

	<div id="myMap">

	</div>

</template>

<script>
	export default {
		data() {
			return {}
		},
		components: { //引入组件

		},
		methods: {
			//创建地图函数：
			createMap() {
				let self = this;
				var map = new BMap.Map("myMap"); //在百度地图容器中创建一个地图
				var point = new BMap.Point(116.683709, 39.88139); //定义一个中心点坐标
				map.centerAndZoom(point, 17); //设定地图的中心点和坐标并将地图显示在地图容器中
				window.map = map; //将map变量存储在全局

				var myKeys = ["韩美林艺术馆"];
				var local = new BMap.LocalSearch(map, {
					renderOptions: {
						map: map,
						panel: "r-result"
					},
					pageCapacity: 5
				});
				local.searchInBounds(myKeys, map.getBounds());

			},
			//地图事件设置函数：
			setMapEvent() {
				map.enableDragging(); //启用地图拖拽事件，默认启用(可不写)
				map.enableScrollWheelZoom(); //启用地图滚轮放大缩小
				map.enableDoubleClickZoom(); //启用鼠标双击放大，默认启用(可不写)
				map.enableKeyboard(); //启用键盘上下左右键移动地图
			},
			initMap() {
				this.createMap(); //创建地图
				this.setMapEvent(); //设置地图事件

			}

		},
		created() { //只执行一次

		},
		mounted() {
						this.initMap();

		}
	}
</script>

<style type="text/css" scoped>
	#myMap {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		margin: auto;
		display: block;
		overflow: hidden;
		z-index: 99;
	}
</style>


