<template>
	<div id="magnumOpus">
		<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
			<div class="content">
				<ul class="magnumOpus-cont">
					<li v-if="(listData.value).length>0" v-for="(list, index) in listData.value" @click="handleJump(list,index)" :id="'current_'+index">
						<div class="div_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.classificationPic+')'}">
							<img :src="imgURL+list.classificationPic" @load="successLoadImg" @error="errorLoadImg" />
						</div>
						<span>{{list.classificationName}}</span>
					</li>
					<li v-if="!listData||listData.length<1">
						<span>{{readyInfo}}</span>
					</li>
				</ul>
			</div>
		</mt-loadmore>
	</div>
</template>

<script>
	import { Indicator } from 'mint-ui';

	export default {
		data() {
			return {
				readyInfo: '',
				listData: {
					totalCount: 0, // 总条数
					pageNumber: 1, // 当前显示页号
					pageSize: 10, // 每页显示数据条数
					value: [], // 显示数据
				},
				allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
				scrollMode: "auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
				loading: false,
				imgURL: this.ApiSever.httpUrl
			}
		},
		created() { //只执行一次
		},
		components: {},
		methods: {
			/*
			 * 获取设计作品欣赏列表
			 */
			getMagnumOpus() {
				let self = this;
				let param = {
					pageNumber: self.listData.pageNumber,
					pageSize:sessionStorage.size?sessionStorage.size:self.listData.pageSize
				};
				Indicator.open('加载中...');
				this.ApiSever.introduce.getMagnumOpus(param).then(res => {
					Indicator.close();
					let result = res.data;
					self.readyInfo = '暂无数据';
					if(result.success) {
						self.listData.value = result.entitys;
						self.listData.totalCount = result.count;
						self.isHaveMore();
						if(sessionStorage.index) {
							let currentId = '#' + sessionStorage.index;
							setTimeout(() => {
								self.goAnchor(currentId);
							}, 50)

						}
					}

				})
			},

			//点击作品跳转到某个详细界面
			handleJump(data,index) {
				sessionStorage.index = 'current_'+index;
				this.$router.push({
					path: '/artappreciation/' + data.id,
					params: {
						id: data.id
					}
				});
			},
			loadBottom() {
				// 上拉加载
				this.toLoadmore(); // 上拉触发的分页查询
				this.$refs.loadmore.onBottomLoaded(); // 固定方法，查询完要调用一次，用于重新定位
			},

			toLoadmore() {
				// 分页查询
				if((this.listData.totalCount)/(this.listData.pageSize) <= 1) {
					this.listData.pageNumber = 1;
					this.allLoaded = true;
				} else {
					this.listData.pageSize = parseInt(this.listData.pageSize) + 10;
					sessionStorage.size=this.listData.pageSize;
					this.allLoaded = false;
				}
				this.getMagnumOpus();
			},
			isHaveMore() {
				this.allLoaded = false;
				let currentNum = parseInt(this.listData.pageSize);
				if(currentNum == this.listData.totalCount || currentNum > this.listData.totalCount) {
					this.allLoaded = true;
				}
			}
		},
		mounted() { //全部渲染完毕
			this.getMagnumOpus();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


