<template>
	<div id="news">
		<div class="swipe-banner" v-if="bannerList&&bannerList.length>0">
			<mt-swipe :auto="4000">
				<mt-swipe-item v-for="(list, index) in bannerList">
					<a href="javascript:;" @click="handleJump(list.id)">
						<div class="div_img bg_cover" :style="{backgroundImage: 'url('+list.bannerPic+')'}">
							<img :src="list.bannerPic" @load="successLoadImg" @error="errorLoadImg" />
						</div>
						<span class="spanBg">{{list.title}}</span>
					</a>
				</mt-swipe-item>
			</mt-swipe>
		</div>
		<div class="mintNavbar">
			<div class="mintTab" @click="hanldeToggle('1')">
				<div class="mintLabel" :class="{'mintActive':checked=='1'}">韩美林资讯</div>
			</div>
			<div class="mintTab" @click="hanldeToggle('2')">
				<div class="mintLabel" :class="{'mintActive':checked=='2'}">本馆资讯</div>
			</div>
			<div class="mintTab" @click="hanldeToggle('0')">
				<div class="mintLabel" :class="{'mintActive':checked=='0'}">过往回顾</div>
			</div>
		</div>
		<div class="content">
			<div class="newsList">
				<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
					<div class="news" v-if="newsList&&newsList.value.length>0" v-for="(list, index) in newsList.value" key="index" @click="handleJump(list.id,index)" :id="'current_'+index">
						<div class="news-lf">
							<div class="title" v-if="list.title">
								{{list.title}}
							</div>
							<span class="time" v-if="list.issueDate">{{list.issueDate}}</span>
						</div>
						<div class="news-rg bg_cover" :style="{backgroundImage: 'url('+list.thumbPic+')'}">
							<img :src="list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
						</div>
						<!--<div class="news-rg bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
							<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
						</div>-->
					</div>
				</mt-loadmore>
			</div>
			<p class="padding" v-if="!newsList||newsList.value.length<1">{{readyInfo}}</p>
		</div>
	</div>
</template>

<script>
	import { Indicator, Toast } from 'mint-ui'

	export default {
		name: 'index',
		data() {
			return {
				readyInfo: '',
				bannerList: [],
				newsList: {
					totalCount: 0, // 总条数
					pageNumber: 1, // 当前显示页号
					pageSize: 10, // 每页显示数据条数
					value: [], // 显示数据
				},
				checked: '1',
				allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
				scrollMode: "auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
				loading: false,
				imgURL: this.ApiSever.httpUrl
			}
		},
		watch: {

		},
		created() {
			if(sessionStorage.type) {
				this.checked = sessionStorage.type;
			}
		},
		methods: {
			//获取banner轮播图数据
			getBanner() {
				this.ApiSever.introduce.getnewsBanner(null).then(res => {
					let result = res.data;
					if(result.success) {
						this.bannerList = result.entitys;
					}
				});
			},

			//获取新闻资讯列表
			getNewsList() {
				let self = this;
				let param = {
					type: sessionStorage.type ? sessionStorage.type : self.checked,
					pageNumber: self.newsList.pageNumber,
//					pageSize: self.newsList.pageSize,
					pageSize:sessionStorage.pageSize ? sessionStorage.pageSize : self.newsList.pageSize,
				};
				Indicator.open('加载中...');
				this.ApiSever.introduce.getNews(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						self.newsList.value = result.entitys;
						self.newsList.totalCount = result.count;
						self.readyInfo = '暂无数据';
						self.isHaveMore();
						if(self.newsList.value && self.newsList.value.length > 0) {
							if(sessionStorage.index) {
								let currentId = '#' + sessionStorage.index;
								setTimeout(() => {
									self.goAnchor(currentId);
								}, 50)

							}
						}

					}
					if(!result.success) {
						Toast({
							message: '加载失败',
							duration: -1
						});
					}
				});
			},

			//获取三馆数据
			getRecommend() {
				this.ApiSever.introduce.getRecommend(null).then(res => {
					let result = res.data;
					if(result.success) {

					}
				});
			},

			//切换事件
			hanldeToggle(index) {
				sessionStorage.removeItem('index');
				sessionStorage.removeItem('pageSize');
				this.checked = index;
				sessionStorage.type = index;
				this.newsList.pageSize = 10;
				this.getNewsList();
			},

			//跳转到某个具体的界面
			handleJump(data, index) {
				sessionStorage.index = 'current_' + index;
				this.$router.push({
					path: '/newsDetail/' + data,
					params: {
						id: data
					}
				});
			},

			loadBottom() {
				this.toLoadmore(); // 上拉触发的分页查询
				this.$refs.loadmore.onBottomLoaded(); // 固定方法，查询完要调用一次，用于重新定位
			},

			toLoadmore() {
				// 分页查询
				if((this.newsList.totalCount) / (this.newsList.pageSize) <= 1) {
					this.newsList.pageNumber = 1;
					this.allLoaded = true;
				} else {
					this.newsList.pageSize = parseInt(this.newsList.pageSize) + 10;
					sessionStorage.pageSize = this.newsList.pageSize;
					this.allLoaded = false;
				}

				this.getNewsList();
			},
			isHaveMore() {
				this.allLoaded = false;
				let currentNum = parseInt(this.newsList.pageSize);
				if(currentNum == this.newsList.totalCount || currentNum > this.newsList.totalCount) {
					this.allLoaded = true;
				}
			}
		},
		mounted() {
			this.getBanner();
			this.getNewsList();
		}
	}
</script>

<style type="text/css" scoped>
	.mintNavbar {
		width: 100%;
		height: 46px;
		padding: 0!important;
		/*padding: 0 0.6875rem !important;*/
		box-sizing: border-box;
		/*border-bottom: 0.125rem solid #f3f3f3;*/
		border-bottom: 4px solid #e5e5e5;
		background-color: #fff;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		margin-top: 4px;
	}
	
	.mintTab {
		padding: 0 !important;
		font-size: 15px;
		display: block;
		-webkit-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		text-decoration: none;
	}
	
	.mintLabel {
		color: #999;
		line-height: 46px;
		font-size: 18px;
		text-align: center;
	}
	
	.mintActive {
		color: #333 !important;
		line-height: 46px !important;
		font-size: 18px !important;
		text-align: center;
		background: #e5e5e5;
	}
	
	.newsList {
		padding: 0 0.6875rem !important;
		box-sizing: border-box;
		margin-bottom: 0.6875rem;
	}
	
	.padding {
		padding-left: 0.6875rem;
	}
</style>


