<template>
	<div id="voice">

		<div class="voice_nav">
			<ul>
				<li @click="languageTogggle('chinese')" :class="{'active_li':language=='chinese'}"><i class="iconfont" v-if="language=='chinese'">&#xe72f;</i> 中</li>
				<li><i class="iconfont">&#xe643;</i></li>
				<li @click="languageTogggle('English')" :class="{'active_li':language=='English'}"><i class="iconfont" v-if="language=='English'">&#xe72f;</i>English</li>
			</ul>
		</div>

		<div v-if="language=='chinese'" class="voice_li" v-for="(list, index) in exhibitionArea" key="index">
			<div class="voice_type">{{list.areaName}}</div>
			<ul v-if="list.exhibitionRoomEntity&&list.exhibitionRoomEntity.length>0">
				<li v-for="(children, item) in list.exhibitionRoomEntity" key="item" @click="handleJump(children.id)">
					<div v-if="children.pic" class="img_box bg_cover" :style="{backgroundImage: 'url('+imgURL+children.pic+')'}">
						<img :src="imgURL+children.pic" @load="successLoadImg" @error="errorLoadImg" />
					</div>
					<!--<div class="img_box" v-if="children.pic">
						<img :src="imgURL+children.pic" :onerror="imgError" />
					</div>-->
					<div class="img_box" v-if="!children.pic">
						<img :src="defaultUrl+'userfiles/defaultImg/defaultMax.jpg'">
					</div>
					<div class="title_ting">
						<span class="title_span">{{children.name}}</span>
						<p>{{children.exhibitionHallName}}</p>
					</div>
					<span>共{{children.itemNum}}件展品</span>
					<i class="iconfont">&#xe601;</i>
				</li>
			</ul>
			<ul v-if="!list.exhibitionRoomEntity||list.exhibitionRoomEntity.length<1">
				<div class="nodata">{{readyInfo}}</div>
			</ul>
		</div>
		<div v-if="language=='English'" class="voice_li" v-for="(list, index) in exhibitionArea" key="index">
			<div class="voice_type">{{list.areaNameEn}}</div>
			<ul v-if="list.exhibitionRoomEntity&&list.exhibitionRoomEntity.length>0">
				<li v-for="(children, item) in list.exhibitionRoomEntity" key="item" @click="handleJump(children.id)">
					<div v-if="children.pic" class="img_box bg_cover" :style="{backgroundImage: 'url('+imgURL+children.pic+')'}">
						<img :src="imgURL+children.pic" @load="successLoadImg" @error="errorLoadImg" />
					</div>
					<!--<div class="img_box" v-if="children.pic">
						<img :src="imgURL+children.pic" :onerror="imgError" />
					</div>-->
					<div class="img_box" v-if="!children.pic">
						<img :src="defaultUrl+'userfiles/defaultImg/defaultMax.jpg'">
					</div>
					<div class="title_ting">
						<span class="title_span">{{children.nameEn}}</span>
						<p>{{children.exhibitionHallIdEn}}</p>
					</div>
					<span>total {{children.itemNum}} exhibits</span>
					<i class="iconfont">&#xe601;</i>
				</li>
			</ul>
			<ul v-if="!list.exhibitionRoomEntity||list.exhibitionRoomEntity.length<1">
				<div class="nodata">{{readyInfoEn}}</div>
			</ul>
		</div>

	</div>
</template>

<script>
	import { Indicator } from 'mint-ui';


	export default {
		data() {
			return {
				readyInfoEn:'',
				readyInfo: '',
				isShow: false,
				language: 'chinese',
				loading: true,
				exhibitionArea: null,
				imgURL: this.ApiSever.httpUrl,
				defaultUrl:this.ApiSever.defaultUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`
			}
		},
		created() {
			if(sessionStorage.language){
				this.language=sessionStorage.language;
			}
		},
		components: {
		},
		methods: {

			//获取列表数据
			getVioceNavList() {
				let info = this.language == 'chinese' ? '加载中...' : 'loading...'
				Indicator.open(info);
//				Indicator.open('数据加载中...');
				this.ApiSever.visit.getVioceNav(null).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						this.readyInfo = '暂无展厅';
						this.readyInfoEn = 'Transient data';
						this.exhibitionArea = result.entity.areaDtos;
					}

				});
			},

			languageTogggle(data) {
				this.language = data;
				sessionStorage.language=data;
			},

			//跳转到某个具体的界面
			handleJump(data) {

				this.$router.push({
					path: '/voiceAudio/' + data + '/' + this.language,
					params: {
						id: data,
						language: this.language
					}
				});
			}
		},
		mounted() {
			this.getVioceNavList();
			//			document.addEventListener('click', (e) => {
			//				if(!this.$el.contains(e.target)) this.isShow = false;
			//			});

		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


