<template>
	<div id="recruit">
		<form action="" class="recruit-form">
			<div><label>姓名：</label>
				<input type="text" placeholder="请输入姓名" v-model="springAutumn.name" name="name" @blur="handleBlur" ref="name" />
				<span class="hiden" ref="name_span">请输入姓名</span>
			</div>

			<div><label>年龄：</label>
				<input type="number" placeholder="请输入年龄" v-model="springAutumn.age" name="age" @blur="handleBlur" ref="age" />
				<span class="hiden" ref="age_span">请输入正确的年龄</span>
			</div>
			<div><label>性别：</label>
				<input type="radio" name="sex" v-model="springAutumn.sex" value="1" />男
				<input type="radio" name="sex" v-model="springAutumn.sex" value="2" />女
			</div>
			<div><label>学校：</label>
				<input type="text" placeholder="请输入学校名称" v-model="springAutumn.school" name="graduateSchool" @blur="handleBlur" ref="school" />
				<span class="hiden" ref="school_span">请输入学校名称</span>
			</div>
			<div><label>联系电话：</label>
				<input type="number" placeholder="请输入联系电话" v-model="springAutumn.tel" name="tel" @blur="handleBlur" ref="tel" />
				<span class="hiden" ref="tel_span">请输入正确的联系电话</span>
			</div>
			<div><label>电子邮箱：</label>
				<input type="email" placeholder="请输入电子邮箱" v-model="springAutumn.mail" name="mail" @blur="handleBlur" ref="mail" />
				<span class="hiden" ref="mail_span">请输入电子邮箱</span>
			</div>
			<div><label>活动意向：</label>
				<input type="text" placeholder="请输入意向活动" v-model="springAutumn.activities" name="activities" ref="activities" />
				<span class="hiden" ref="activities_span">请输入意向活动</span>
			</div>
			<div><label>备注：</label>
				<textarea placeholder="请输入备注" v-model="springAutumn.remarks"></textarea>
			</div>
			<span class="a_herf" @click="addpubliceducation">提 交</span>

		</form>
	</div>

</template>

<script>
	import { Navbar, TabItem, Field, Radio, Indicator, Toast, MessageBox } from 'mint-ui'

	//文本框校验正则
	let rejson = {
		name: /\S/, //校验文本框不为空
		age: /^(?:[1-9][0-9]?|1[01][0-9]|120)$/,
		tel: /^([0-9]{3,4}-)?[0-9]{7,8}$/, //校验座机号码
		mail: /^\w+@[a-zA-Z0-9\-]+(\.[a-zA-Z]{2,6}){1,2}$/,
		activities: /\S/
	};

	//手机号码校验
	let isMob = /^((\+?86)|(\+86))?(13[012356789][0-9]{8}|15[012356789][0-9]{8}|18[02356789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/;

	export default {
		data() {
			return {
				springAutumn: {
					name: '',
					sex: 1,
					age: '',
					school: '',
					tel: '',
					mail: '',
					remarks: '',
					activities: ''
				}
			}
		},
		created() { //只执行一次
			this.titleInfo = this.$route.meta.name;
			this.getPublicEducationTitle();
		},
		components: {
		},
		methods: {
			//通过活动ID获取活动名称
			getPublicEducationTitle() {
				let param = {
					id: this.$route.params.id
				};

				this.ApiSever.service.getPublicEducationDetail(param).then(res => {
					let result = res.data;

					if(result.success) {
						this.springAutumn.activities = result.entity.title;
					}
				});
			},

			//鼠标移出文本框事件
			handleBlur(event) {
				let currentEl = event.target.name;
				if(rejson[currentEl]) {
					if(currentEl == 'tel') {
						if(rejson[currentEl].test(event.target.value) || isMob.test(event.target.value)) {
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					} else {
						if(rejson[currentEl].test(event.target.value)) {
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					}
				}
			},

			//点击提交按钮事件
			addpubliceducation() {
				let bOk = true;
				for(let name in rejson) {
					if(name == 'tel') {
						if(rejson[name].test(this.springAutumn[name]) || isMob.test(this.springAutumn[name])) {
							this.$refs[name + '_span'].className = 'hiden';
						} else {
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						}
					} else {
						if(!rejson[name].test(this.springAutumn[name])) {
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						} else {
							this.$refs[name + '_span'].className = 'hiden';
						}
					}
				}

				if(!bOk) {
					return false;
				}

				let param = Object.assign({}, this.springAutumn);
				param.actiId = this.$route.params.id;

				delete param.activities;

				this.ApiSever.service.addpubliceducation(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.$toast({
							message: '报名成功！',
							iconClass: 'icon icon-success'
						});
						this.$router.push({
							path: '/publicEducation'
						});
					} else {
						switch(result.message) {
							case -2:
								MessageBox.alert('此活动您已报名，不能重复报名！');
								break;
							case -3:
								MessageBox.alert('您还没授权！');
								break;
							case -4:
								MessageBox.alert('程序查询内部错误！');
								break;
							default:
								break;
						}
					}
					//					let result = res.data;
					//					Indicator.close();
					//					if(result.success) {
					//						this.$toast('报名成功！')
					//						this.$router.push({
					//							path: '/publicEducation'
					//						});
					//					}
				})
			}

		},

		mounted() {

		}
	}
</script>

<style type="text/css" scoped>
	.mint-tab-container {
		background: #fff;
	}
	
	.promptData {
		width: 100%;
		padding-left: 5rem;
		box-sizing: border-box;
		display: block;
		color: red;
		clear: both;
	}
	
	.input_div .errShow {
		padding-left: 0 !important;
	}
	
	.errShow {
		width: 100%;
		height: 0.4375rem;
		line-height: 0.4375rem;
		display: block;
		color: red;
		padding-left: 5rem;
		box-sizing: border-box;
	}
	
	.hiden {
		display: none;
	}
</style>


