<template>
	<div id="libraryWork">
		<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
			<div class="waterfall-flow" id="waterfallFlow">
				<ul>
				</ul>
				<ul>
				</ul>
			</div>
		</mt-loadmore>
	</div>
</template>

<script>
	import { Indicator } from 'mint-ui'

	export default {
		data() {
			return {
				libraryWorkList: {
					totalCount: 0, // 总条数
					pageNumber: 1, // 当前显示页号
					pageSize: 10, // 每页显示数据条数
					value: [], // 显示数据
				},
				allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
				scrollMode: "auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
				imgURL: this.ApiSever.httpUrl
			}
		},
		created() { //只执行一次

		},
		components: {},
		methods: { //执行的方法函数
			getLibraryWorkList() {
				let self = this;
				let param = {
					pageNumber: self.libraryWorkList.pageNumber,
					pageSize: sessionStorage.size?sessionStorage.size:self.libraryWorkList.pageSize
				};
				Indicator.open('加载中...');
				this.ApiSever.introduce.getLibraryWork(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						self.libraryWorkList.value = result.entitys;
						self.libraryWorkList.totalCount = result.count;
						self.isHaveMore();
						this.appendLi();
//						if(sessionStorage.index) {
//							let currentId = '#' + sessionStorage.index;
//							setTimeout(() => {
//								self.goAnchor(currentId);
//							}, 500)
//
//						}
					}
				})
			},
			loadBottom() {
				// 上拉加载
				this.toLoadmore(); // 上拉触发的分页查询
				this.$refs.loadmore.onBottomLoaded(); // 固定方法，查询完要调用一次，用于重新定
			},

			toLoadmore() {
				// 分页查询
				if((this.libraryWorkList.totalCount) / (this.libraryWorkList.pageSize) <= 1) {
					this.libraryWorkList.pageNumber = 1;
					this.allLoaded = true;
				} else {
					//					this.listData.pageNumber = parseInt(this.listData.pageNumber) + 1;
					this.libraryWorkList.pageSize = parseInt(this.libraryWorkList.pageSize) + 10;
					sessionStorage.size=this.libraryWorkList.pageSize;
					this.allLoaded = false;
				}
				this.getLibraryWorkList();
			},
			isHaveMore() {
				this.allLoaded = false;
				let currentNum = parseInt(this.libraryWorkList.pageSize);
				if(currentNum == this.libraryWorkList.totalCount || currentNum > this.libraryWorkList.totalCount) {
					this.allLoaded = true;
				}
			},

			//跳转到某个具体的界面
			handleJump(data) {
				this.$router.push({
					path: '/libraryDetail/' + data,
					params: {
						id: data
					}
				});
			},

			createLi(data) {
				let self = this;
				var oLi = document.createElement('li');
				var oDiv = document.createElement('div');
				var oSpan = document.createElement('span');
				oLi.id='current_'+ data.id;
				var oImg = new Image();
				oDiv.className = "libraryWork-img";
				oImg.src = self.imgURL + data.thumbPic;
				oSpan.innerHTML = data.name;
				oImg.onerror = function() {
					oImg.src = self.imgURL + '/userfiles/defaultImg/defaultMin.jpg';
				}

				oDiv.appendChild(oImg);
				oLi.appendChild(oDiv);
				oLi.appendChild(oSpan);

				oLi.onclick = function() {
					sessionStorage.index = 'current_'+ data.id;
					self.$router.push({
						path: '/libraryDetail/' + data.id,
						params: {
							id: data.id
						}
					});
				}
				return oLi;
			},

			appendLi() {
				let oAaterfallFlow = document.getElementById('waterfallFlow');
				let aUl = oAaterfallFlow.getElementsByTagName('ul');
				aUl[0].innerHTML = '';
				aUl[1].innerHTML = '';

				let resultData = [...this.libraryWorkList.value];
				if(!resultData || resultData.length < 1) {
					aUl[0].innerHTML = "<p>暂无数据</p>"
					return false;
				}

				for(var i = 0, ilen = resultData.length; i < ilen; i++) {
					var oLi = this.createLi(resultData[i]);

					var arr = [];
					for(var j = 0, len = aUl.length; j < len; j++) {
						arr.push(aUl[j]);
					}

					arr.sort(function(u1, u2) {
						console.log(u1.clientHeight,u2.clientHeight);
						return u1.scrollHeight - u2.scrollHeight;
					});

					arr[0].appendChild(oLi);
				}

			}

		},
		mounted() { //全部渲染完毕
			this.getLibraryWorkList();
			let _this = this;

			window.onscroll = window.onresize = function() {
				let scrollT = document.documentElement.scrollTop || document.body.scrollTop;
				let clientHeight = document.documentElement.clientHeight;
				if(scrollT + clientHeight >= document.body.scrollHeight - 500) {}
			};

		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>
	#items {
		opacity: 0;
	}
</style>


