In 2004, he received the World Art Master Prize from the World Artists Association of America and the President's Award for Educational Excellence issued by former American President George W. Bush. In 2011, he was designated the “Most Followed Chinese Figure in the World” by Global Times and received the “Lifetime Achievement Award for Contribution to Chinese Literature and Art” from China’s Ministry of Culture. A “Grand Exhibition of Art by Han Meilin” was held at the National Museum of China, with unprecedented popularity. In 2012, his sculpture series, “Mother and Child,” received the Copyright Gold Medal from the World Intellectual Property Organization. On December 21, 2013, the first “Han Meilin Day” was declared in China. In 2015, UNESCO designated Han Meilin as an “Artist of Peace,” the first Chinese artist to receive this honor. There are Han Meilin Art Museums in the cities of Hangzhou, Beijing and Yinchuan, further promoting art education and international cultural exchange. From 2016 to 2017, “Han Meillin World Tour Exhibition” were successfully held in Venice, Beijing and Paris. ")]), _vm._v(" "), _c('p', [_vm._v("\n\n\t\t\t\t\tHis signature works include: A collection of ancient Chinese calligraphy, “The Sealed Book;” giant city sculptures, “Howling at the Wind,” “Shun Plowing a Field,” “Birds of Good Luck,” “The Dragon of Qian River,” “God of Qian River Shooting at the Tide,” “Fire Phoenix,” “The Fire Phoenix Welcoming Harmony,” “One Hundred Birds Honoring the Phoenix,” “The Phoenix Honoring the Sun,” and “The Gate of South Lake;” the designs of: the Air China logo, the interior design of a new fleet of Air China airplanes, the logo of the 2008 Beijing Olympic bid, the “Fuwa” mascots of the 2008 Beijing Olympics, the the logo for the Beijing Tourism Office, the theme for the Hong Kong and Macau travel permit, the logo of the China-Russia Youth Year, the logo for the Lancang-Mekong Cooperation Leaders’ Meeting, etc.")]), _vm._v(" "), _c('p', [_vm._v("\n\n\t\t\t\t\tHan Meilin’s work is part of the collections of the National Museum of China, the State Russian Museum, the British Museum, the Egyptian Museum, the Institut de France, and the Massachusetts Institute of Technology, among others.")]), _vm._v(" "), _c('p', [_vm._v("\n\n\t\t\t\t\tHan Meilin’s publications include: Mountain Flowers in Full Bloom, Meilin, Self-Selected Sculptures by Han Meilin, Self-Selected Paintings by Han Meilin, Bloom of Beauty: Figure Drawings by Han Meilin, Lessons with Han Meilin: Manuscript of Figure Drawings, Mountain and River: A Collection of Han Meilin's Landscape Paintings, A Collection of Han Meilin Calligraphy, Han Meilin Calligraphy: Freestyle (Five Volumes), Perseverance: Manuscript of Han Meilin Calligraphy, Art by Han Meilin, New Theories on Han Meilin’s Art, The Book of Rites: Datong, the Heart Sutra: Freestyle, Collection of Han Meilin’s Rock Paintings, and the essay collections, Some Ideas, Han Meilin in His Own Words, Essays by Han Meilin, and the Future is Unknown, etc.")])]), _vm._v(" "), _c('span', {
