<template>
	<div id="cultureProducts">

		<div class="content" v-if="detailData">
			<div class="detail-swipe" v-if="detailData&&detailData.images&&detailData.images.length>0">
				<mt-swipe :auto="4000">
					<mt-swipe-item v-for="(list,index) in detailData.images" key="index">
						<!--<figure class="figure">-->
						<a :href="imgURL+list.thumbPic+'?960x30000'"  >
							<div class="detailBanner_swipe">
								<img :src="imgURL+list.thumbPic" :onerror="imgError" />
							</div>
						</a>
					</mt-swipe-item>

				</mt-swipe>
			</div>
			<h1 v-if="detailData.name">{{detailData.name}}</h1>
			<span v-if="detailData.texture" class="librar_size">材质：{{detailData.texture}}
			</span>
			<span v-if="detailData.spec" class="librar_size"> 尺寸：{{detailData.spec}}</span>
			<span v-if="detailData.creationDate" class="librar_size">
				年代：{{detailData.creationDate}}
			</span>
			<div v-if="detailData.content" v-html="detailData.content">

			</div>
			<div v-if="!detailData||!detailData.content">
				<p>{{readyInfo}}</p>
			</div>
		</div>

		<!--<div class="pswp" tabindex="-1" role="dialog" aria-hidden="true">
			<div class="pswp__bg"></div>
			<div class="pswp__scroll-wrap">
				<div class="pswp__container">
					<div class="pswp__item"></div>
					<div class="pswp__item"></div>
					<div class="pswp__item"></div>
				</div>
				<div class="pswp__ui pswp__ui--hidden">

					<div class="pswp__top-bar">
						<div class="pswp__counter"></div>
						<button class="pswp__button pswp__button--close" title="Close (Esc)"></button>
						<button class="pswp__button pswp__button--share" title="Share"></button>
						<button class="pswp__button pswp__button--fs" title="Toggle fullscreen"></button>
						<button class="pswp__button pswp__button--zoom" title="Zoom in/out"></button>
						<div class="pswp__preloader">
							<div class="pswp__preloader__icn">
								<div class="pswp__preloader__cut">
									<div class="pswp__preloader__donut"></div>
								</div>
							</div>
						</div>
					</div>
					<div class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap">
						<div class="pswp__share-tooltip"></div>
					</div>
					<button class="pswp__button pswp__button--arrow--left" title="Previous (arrow left)"></button>
					<button class="pswp__button pswp__button--arrow--right" title="Next (arrow right)"></button>
					<div class="pswp__caption">
						<div class="pswp__caption__center"></div>
					</div>
				</div>
			</div>
		</div>-->
	</div>
</template>

<script>
	import { Indicator } from 'mint-ui';

	let testData = [{}];

	export default {
		data() {
			return {
				readyInfo: '',
				detailData: null,
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.httpUrl}/userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		created() { //只执行一次
			this.titleInfo = this.$route.meta.name;

		},
		components: {},
		methods: { //执行的方法函数
			getLibraryDetail() {
				let self = this;
				Indicator.open('加载中...');
				let param = {
					id: this.$route.params.id ? this.$route.params.id : null
				};
				this.ApiSever.introduce.getLibraryDetail(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						self.detailData = result.entity;
						self.titleInfo = result.entity.name;
						self.readyInfo = '暂无数据';
						window.document.title = result.entity.name;
					}
				})
			}

		},
		mounted() { //全部渲染完毕		 	
			this.getLibraryDetail();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


