<template>
	<div id="cultureProducts">

		<div class="content">
			<div class="banner">
				<div class="detail-swipe" v-if="detaiList.images&&detaiList.images.length>0">
					<mt-swipe :auto="4000">
						<mt-swipe-item v-for="(list,index) in detaiList.images" key="index">
							<!--<div class="detailBanner_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
						<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
					</div>-->
							<a :href="imgURL+list.thumbPic+'?960x30000'" >
								<div class="detailBanner_swipe">
									<img :src="imgURL+list.thumbPic" :onerror="imgError" />
								</div>
							</a>
						</mt-swipe-item>
					</mt-swipe>
				</div>
				<h1 v-if="detaiList.name">{{detaiList.name}}</h1>
				<span v-if="detaiList.price" class="librar_size">价格：{{detaiList.price}}</span>
				<div v-if="detaiList&&detaiList.descr" v-html="detaiList.descr">

				</div>
				<div v-if="!detaiList||!detaiList.descr">
					<p>{{readyInfo}}</p>
				</div>

			</div>
			<div v-if="detaiList.relateCulturalProducts&&detaiList.relateCulturalProducts.length>0">

				<div class="title_msg">
					<span>相关产品</span>
				</div>
				<div class="related-products">
					<ul>
						<li v-for="(list,index) in detaiList.relateCulturalProducts" key="index" @click="handleJump(list)">

							<div class="div_box bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
								<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
							</div>
						</li>
					</ul>
				</div>
			</div>
		</div>

		<span v-if="!detaiList.link" class="unPurchasing">微店购买</span>
		<span v-if="detaiList.link" class="purchasing"><a :href="detaiList.link">微店购买</a></span>
		<div class="interlaced"></div>
	</div>
</template>

<script>
	import { MessageBox, Indicator } from 'mint-ui'

	export default {
		data() {
			return {
				readyInfo:'',
				photoData: [],
				detaiList: [],
				parenId: null,
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		watch: {
			'$route' (to, from) {
				this.getCultureProductsDetail();
			}
		},
		created() {},
		components: {},
		methods: {
			getCultureProductsDetail() {
				Indicator.open('加载中...');
				let self = this;
				let param = {
					id: this.$route.params.id ? this.$route.params.id : null
				};

				this.ApiSever.service.getCultureProductsDetail(param).then(res => {
					let result = res.data;
					Indicator.close();
					if(result.success) {
						self.detaiList = result.entity;
						self.titleInfo = result.entity.name;
						self.readyInfo='暂无数据';
						window.document.title = result.entity.name;

					}
				});
			},

			//跳转到详细界面
			handleJump(data) {
				this.$router.push({
					path: '/cultureDetail/' + data.id,
					params: {
						id: data.id
					}
				});
			}
		},
		mounted() {
			this.getCultureProductsDetail();

		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>
	.related-products {
		margin: 0.6875rem 0;
	}
	
	.purchasing {
		/*position: fixed;
		/*bottom: 2.8125rem;*/
		/*bottom: 55px;
		left: 0;
		right: 0;*/
		width: 100%;
		height: 1.875rem;
		display: block;
		background: #c22f2d;
		color: #fff;
		font-size: 0.78125rem;
		line-height: 1.875rem;
		margin-top: 0.4rem;
		text-align: center !important;
	}
	
	.purchasing a {
		color: #fff;
		text-align: center;
	}
	
	.unPurchasing {
		width: 100%;
		height: 1.875rem;
		display: block;
		background: #c8c8c8;
		color: #fff;
		font-size: 0.78125rem;
		line-height: 1.875rem;
		margin-top: 0.4rem;
		text-align: center !important;
	}
</style>


