<template>
	<div id="artProfile">
		<div class="banner" id="back_top">
			<!--<img :src="imgURL+'/userfiles/defaultImg/bjgBanner.jpg'"/>-->
			<img :src="imgURL+bannerBj" />
		</div>
		<div class="content">
			<div class="banner">
				<h1>北京韩美林艺术馆</h1>
				<Photoswipe v-if="isShow" :data="photoData"></Photoswipe>
				<div class="unfurled_describe" :class="{'unfurled_active':isShow}" v-html="info.chinese">

				</div>
				<div class="unfurled_describe" :class="{'unfurled_active':isShow}" v-html="info.english">

				</div>
				<div class="pavilion_impression">
					<span>展馆印象</span>
				</div>
				<Photoswipe v-if="!isShow" :data="photoData"></Photoswipe>

				<!--<span class="more clearfix" @click="handleMore">{{moreInfo}}</span>-->
				<span class="unfurled" @click="handleShow">{{showInfo}}</span>
			</div>
			<div class="title_msg">
				<span>虚拟展厅</span>
			</div>
			<div class="panorama3D">
				<a href="http://www.artronpano.com/scene/ZTuePeLlkfuPNjz2ZHmVzhpmMzDoVS3M/bjhml//tour.html">
					<img src="../../../assets/image/panorama3D02.jpg" alt="" />
					<div class="panorama3D_mask"></div>
					<div class="exhibition_btn">
						<img src="../../../assets/image/exhibition_btn.png" alt="" class="panorama3D_btn" />
						<span class="panorama3D_dec">全景展览</span>
					</div>
				</a>

			</div>
			<div class="exhibition-works" v-if="listData&&listData.length>0">
				<div class="title_msg">
					<span>馆藏作品</span>
				</div>
				<ul class="exhibition-img">
					<li v-for="(list, index) in listData" @click="handleJump(list.id)">
						<div class="div_minimg bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
							<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
						</div>
						<span>{{list.name}}</span>
					</li>
					<li class="div-more">
						<div>
							<a href="#libraryWork">
								<i class="icon iconfont icon-gengduo"></i> 更多
							</a>
						</div>
					</li>
				</ul>
			</div>
			<div class="visit">
				<VisitInformation></VisitInformation>
			</div>
		</div>

	</div>
</template>

<script>
	import VisitInformation from '../../component/visitInformation.vue'
	import Photoswipe from '../../component/photoswipe.vue'

	let abbreviate = {
		chinese: '<p>2006年，韩美林先生将几十年来潜心创作的2000件艺术作品捐赠给了北京通州区人民政府。为了收藏、陈列这些珍贵的作品，展示韩美林先生对祖国、对中华民族文化的热爱以及传播...</p>',
		english: '<p>In 2006, Mr. Han Meilin donated 2,000 of his works of art to the Tongzhou District Government of Beijing. To house and exhibit this precious gift, to show Mr. Han’s love ...<p>'
	};
	let normal = {
		chinese: '<p>2006年，韩美林先生将几十年来潜心创作的2000件艺术作品捐赠给了北京通州区人民政府。为了收藏、陈列这些珍贵的作品，展示韩美林先生对祖国、对中华民族文化的热爱以及传播他的美学思想，通州区政府在梨园文化主题公园内开始兴建一座面积近10000平米、融合东西方建筑美学理念的艺术殿堂。2008年6月25日，北京韩美林艺术馆在通州正式落成。这座静谧大气的建筑不仅成为了首都北京的文化宝藏与人文景观，并且与2005年10月19日开幕、位于京杭大运河另一端的“杭州韩美林艺术馆”相互辉映，彰显出大运河的厚重历史与文化张力，将一种深沉的地缘和文化归属感传递给了有着南北血脉及浓烈爱国情怀的韩美林先生。</p><p>北京韩美林艺术馆装修改造工程于2013年6月竣工，扩建后的全馆总建筑面积约12000平方米，形成了集展示、教学、互动、休闲等多功能于一体的空间布局。全馆现有南北两个展区，其中包括北展区的序厅、陶瓷馆、综合馆、雕塑馆、书画馆、新作馆、影视厅，以及南展区的设计馆、手稿馆、紫砂馆、城雕厅，展品共2600余件，全面、系统地展示了韩美林先生的艺术成就、多方面的艺术才华和无限的创造力。</p><p>该馆由我国著名建筑师崔铠先生设计。他的设计理念是：艺术馆作为梨园主题公园的一部分，其位置与形态对公园的空间格局产生了重要影响。如果我们把整个公园看作是一幅自然山水画，那么艺术馆正好是作画完毕盖的一个印章。印章与画的关系对应着艺术馆与公园的关系，彼此缺一不可，相得益彰。而书法与篆刻恰好是韩先生艺术创作的一部分，因此选用“美”字作为艺术馆的平面图案原型，将书法的笔画与建筑的功能空间相对应。同时，“美”字既为韩先生名字中的一字，又可理解为美学、艺术之美，具有写实与抽象的双重特性。</p><p>艺术馆坚持“以人为本”的发展理念，全体工作人员努力为大众提供最专业、最人性化的服务。在韩美林先生的艺术理念和建馆宗旨的引导下，艺术馆开馆至今一直实行免费参观制度，希望能够吸引更多的艺术爱好者前来感受创造之美。</p><p>2013年，北京韩美林艺术馆正式被国家旅游局评定为国家AAAA级旅游景区，是全国唯一被评为国家AAAA级旅游景区的个人艺术馆，更加巩固了韩美林艺术馆在全国文化艺术传播机构中的重要地位。</p><p>在这里，您不仅可以感受到美林先生自由的心，率真的爱，深厚的情和神奇的笔，还可以体悟一位艺术家的风雨人生，看到一位在各个艺术领域中均取得非凡成就的“全能型大家”的面貌。他就像一座活火山，不断喷涌着艺术的岩浆，永难停歇。如同韩美林先生所说：余此生只抓一个“艺”字为“耕”，收拾凡心，不思功名，不谋衣冠，上苍告诉我：“韩美林，你就是头牛，这辈子你就干活吧！”</p>',
		english: '<p>In 2006, Mr. Han Meilin donated 2,000 of his works of art to the Tongzhou District Government of Beijing. To house and exhibit this precious gift, to show Mr. Han’s love of the country and Chinese culture, and to disseminate his aesthetic ideas, the local government built an art gallery in the Liyuan Cultural Theme Park. Two years later, a Palace in Art—Beijing Han Meilin Art Gallery opened to the public on June 25, 2008. It covers an area of nearly 10,000 square meters, combining the ideals of man and nature and integrating Eastern and Western architectural aesthetics. Quiet, spacious and bright, it interweaves time and space and displays unique architectural excellence, further adding cultural significance to the human landscape of the capital city of Beijing. It also shines in concert with the Hangzhou Han Meilin Art Gallery (opened October 19, 2005) located at the other end of the ancient Great Canal. Blending the historic and the modern, this cultural pattern highlights the attraction of the ancient canal’s history and culture. Mr. Han Meilin, whose father comes from Shandong in northern China and whose mother was born in Zhejiang in southern China, is a patriotic artist with a strong sense of belonging and gratitude to China.</p><p>In addition to the Introductory Hall, Arts and Crafts Hall, Paperboard Ornamental Hall, Sculpture Hall, Chinese Calligraphy and Traditional Painting Hall, Newly-Created Works Hall, Video Hall, and Sculpture-Making Studio, there is the Seal-Book Building, and the Pottery Bar. Such cultural facilities provide visitors with an interactive cultural platform where they can enjoy the beauty of art in genetral as well as that of Chinese civilization.</p><p>Beijing Han Meilin Art Gallery was designed by the famous Chinese architect Kai Cui. His design concept is that the art gallery’s site and form have an important impact on Liyuan Park’s space as an integral part of the Cultural Theme Park. If we take the whole park as a natural landscape painting, the art gallery serves as a traditional Chinese seal affixed just when the painting is finished. The relation between the seal and the painting corresponds to that of the art gallery and the park. They are indispensible to and complementary to each other. Considering that Chinese calligraphy and seal cutting are also an aspect of Mr. Han’s artistic creation, the designer chose the Chinese character for “beauty” (美 mei) as the graphic design prototype of the art gallery. Looking down from above, the shape of the entire art gallery is like a “美” ; thus the strokes of the Chinese calligraphic character represent  the architecture’s functional space. Significantly, the word “beauty” (美 mei) is also second word of Han Meilin’s name which can also be understood as representing aesthetics and artistic beauty. These explanations are of the realistic and of the abstract as well.</p><p>Our art gallery adheres to the “people-oriented development philosophy”, and the staff makes great efforts to provide the public with the most professional, most people-oriented services. In keeping with Mr. Han Meilin’s noble sentiments, art philosophy and purpose for founding the art gallery, guests have enjoyed free admission to the art gallery since opening day. We will be glad to see more art lovers come to visit this palace of art. Here, visitors can appreciate countless wonderful works of art created by Han Meilin with his open-heated, forthright and sincere love, deep emotion and magic whiteboard marker pens and brushes, all of which most ordinary people can’t imagine. Visitors can also experience a great artist’s life and better understand a versatile artist who has achieved extraordinary success in almost all artistic aspects. Mr. Han is like an active volcano which is incessantly erupting with artistic lava – the flower never ceases. As Mr. Han has said, “I just work hard on my art throughout my life, not for money, scholarly honors or official rank. It is God’s way of telling me that since I’m as stubborn as an ox, I’ll have to work as hard as one.”</p>'
	};

	export default {
		data() {
			return {
				bannerBj: pavilionData.bannerBj,
				photoData: pavilionData.artDataybj,
				isShow: false,
				showInfo: '展开',
				info: abbreviate,
				listData: [],
				//				photoData: photoData,
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		created() { //只执行一次
			this.titleInfo = this.$route.meta.name;
		},
		components: {
			VisitInformation,
			Photoswipe
		},
		methods: {
			getLibraryWorkList() {
				let param = {
					pageSize: 3,
					pageNo: 1
				};
				this.ApiSever.introduce.getLibraryWork(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.listData = result.entitys;
					}
				})
			},

			//展开和收起操作
			handleShow() {
				this.isShow = !this.isShow;
				if(this.isShow) {
					this.info = Object.assign({}, normal);

					this.showInfo = '收起';
				} else {
					this.info = Object.assign({}, abbreviate);
					document.body.scrollTop = 0;
					this.showInfo = '展开';
				}

			},

			//跳转到某个具体的界面
			handleJump(data) {
				this.$router.push({
					path: '/libraryDetail/' + data,
					params: {
						id: data
					}
				});
			}
		},
		mounted() { //全部渲染完毕
			this.getLibraryWorkList();
		}
	}
</script>

<style type="text/css" scoped>

</style>


