<template>
	<div id="rockPaintings">
		<div class="content" >
			<ul class="rockPaintings-top" id="back_top">
				<li>
					<div id="rockvideo" class="div_img bg_cover" style="background-image: url(http://hanml.artup.com/userfiles/defaultImg/rockPainting.jpg)">	
						<img class="videoBG" src="http://hanml.artup.com/userfiles/defaultImg/rockPainting.jpg" @load="successLoadImg" @error="errorLoadImg" />
						<!--<img class="videoBG" src="../../../assets/image/documentaryBG.png" />-->
						<img class="videobtn" src="../../../assets/image/videoBtn.png" @click="playVideo" />
						<video id="myVideo" :src="videoUrl+'userfiles/1/videos/rockPaintings.mp4'" width="100%" height="100%" controls="controls" style="display: none;">
							Your browser does not support the video tag.
						</video>
					</div>
					<!--<div class="div_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.classificationPic+')'}">
						<img class="videoBtn" src="../../../assets/image/videoBtn.png" alt="" />
						<img :src="imgURL+list.classificationPic" @load="successLoadImg" @error="errorLoadImg" />
					</div>-->
					<h1>银川馆岩画宣传视频</h1>
					<div class="unfurledEn_describe" :class="{'unfurled_active':isShow}">
						<p>韩美林与贺兰山岩画，穿越5000年的时空连线。他以5000年的长途大礼拜，对中国传统文化做了诚笃的顶礼。通过这条连线，使韩美林的艺术有了更远渊源的寻找。贺兰山岩画连线韩美林艺术，是中国传统文化生命的延续。</p>
					</div>
				</li>

				<span class="unfurled" @click="handleShow">{{showInfo}}</span>
			</ul>

		</div>
		<div class="partition"></div>
		<div id="libraryWork">
			<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
				<div class="waterfall-flow" id="waterfallFlow">
					<ul>
					</ul>
					<ul>
					</ul>
				</div>
			</mt-loadmore>
		</div>

	</div>
</template>

<script>
	import { Indicator } from 'mint-ui';

	export default {
		data() {
			return {
				rockPaintingsList: {
					totalCount: 0, // 总条数
					pageNumber: 1, // 当前显示页号
					pageSize: 10, // 每页显示数据条数
					value: [], // 显示数据
				},
				showInfo: '展开',
				isShow: false,
				imgURL: this.ApiSever.httpUrl,
				videoUrl: this.ApiSever.videoUrl,
				allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
				scrollMode: "auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
			}
		},
		created() {},
		components: {},
		methods: {
			getRockPaintingsList() {
				let self = this;
				let param = {
					pageNumber: self.rockPaintingsList.pageNumber,
					pageSize: sessionStorage.size?sessionStorage.size:self.rockPaintingsList.pageSize
				};
				Indicator.open('加载中...');
				this.ApiSever.visit.getRockpaintings(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						self.rockPaintingsList.value = result.entitys;
						self.rockPaintingsList.totalCount = result.count;
						self.isHaveMore();
						self.appendLi();
						if(sessionStorage.index) {
							let currentId = '#' + sessionStorage.index;
							setTimeout(() => {
								self.goAnchor(currentId);
							}, 50)

						}
					}
				})
			},

			createLi(data) {
				let self = this;
				var oLi = document.createElement('li');
				var oDiv = document.createElement('div');
				var oSpan = document.createElement('span');
				oLi.id='current_'+data.id;
				var oImg = new Image();
				oDiv.className = "libraryWork-img";
				oImg.src = self.imgURL + data.thumbPic;
				oSpan.innerHTML = data.name;
				oImg.onerror = function() {
					oImg.src = self.imgURL + '/userfiles/defaultImg/defaultMin.jpg';
				}

				oDiv.appendChild(oImg);
				oLi.appendChild(oDiv);
				oLi.appendChild(oSpan);

				oLi.onclick = function() {
					sessionStorage.index = 'current_'+data.id;
					self.$router.push({
						path: '/rockPaintingDetail/' + data.id,
						params: {
							id: data.id
						}
					});
				}
				return oLi;
			},

			appendLi() {
				let oAaterfallFlow = document.getElementById('waterfallFlow');
				let aUl = oAaterfallFlow.getElementsByTagName('ul');
				aUl[0].innerHTML = '';
				aUl[1].innerHTML = '';

				let resultData = [...this.rockPaintingsList.value];
				if(!resultData || resultData.length < 1) {
					aUl[0].innerHTML = "<p>暂无数据</p>"
					return false;
				}

				for(var i = 0, ilen = resultData.length; i < ilen; i++) {
					var oLi = this.createLi(resultData[i]);

					var arr = [];
					for(var j = 0, len = aUl.length; j < len; j++) {
						arr.push(aUl[j]);
					}

					arr.sort(function(u1, u2) {
						return u1.scrollHeight - u2.scrollHeight;
					});

					arr[0].appendChild(oLi);
				}

			},
			
			//展开和收起操作
			handleShow() {
				this.isShow = !this.isShow;
				if(this.isShow) {
					this.showInfo = '收起';
				} else {
					this.showInfo = '展开';
					document.body.scrollTop = 0;
				}
			},

			loadBottom() {
				// 上拉加载
				this.toLoadmore(); // 上拉触发的分页查询
				this.$refs.loadmore.onBottomLoaded(); // 固定方法，查询完要调用一次，用于重新定位
			},

			toLoadmore() {
				// 分页查询
				if((this.rockPaintingsList.totalCount) / (this.rockPaintingsList.pageSize) <= 1) {
					this.rockPaintingsList.pageNumber = 1;
					this.allLoaded = true;
				} else {
					this.rockPaintingsList.pageSize = parseInt(this.rockPaintingsList.pageSize) + 10;
					sessionStorage.size=this.rockPaintingsList.pageSize;
					this.allLoaded = false;
				}
				this.getRockPaintingsList();
			},
			isHaveMore() {
				this.allLoaded = false;
				let currentNum = parseInt(this.rockPaintingsList.pageSize);
				if(currentNum == this.rockPaintingsList.totalCount || currentNum > this.rockPaintingsList.totalCount) {
					this.allLoaded = true;
				}
			},

			//播放视频事件
			playVideo(event) {
				this.stopVideo('rockvideo');
				let currentNum = event.currentTarget.dataset.index;
				let currentVideo = document.getElementById('myVideo');
				let currentImg = document.getElementsByClassName('videoBG')[0];
				let parent = event.target.parentNode;
				event.target.style.display = "none";
				parent.style.backgroundImage = '';
				parent.style.height = 'auto';
				currentImg.style.display = 'none';
				currentVideo.style.display = 'block';
				currentVideo.play();
			}
		},
		mounted() { //全部渲染完毕
			this.getRockPaintingsList();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


