<template>
	<div id="Message">

		<span class="msg_span">艺术馆感谢您的支持与陪伴</span>
		<span class="msg_span">真诚的希望得到您的宝贵意见和建议</span>
		<span class="msg_span">我们将竭诚为您提供更好的服务</span>

		<div class="form_box" id="message_box">
			<textarea placeholder="请留下您的宝贵意见" v-model="messageboard.content" class="span_textarea" name="content" rows="" cols="" @blur="handleBlur" ref="content"></textarea>
			<span class="hiden" ref="content_span">意见不能为空</span>
			<input placeholder="联系人" v-model="messageboard.contacts" class="text_input" type="text" name="contacts" @blur="handleBlur" ref="contacts" />
			<span class="hiden" ref="contacts_span">联系人不能为空</span>
			<input placeholder="联系电话" v-model="messageboard.contactsPhone" class="text_input" type="tel" name="contactsPhone" @blur="handleBlur" ref="contactsPhone" />
			<span class="hiden" ref="contactsPhone_span">请输入正确的联系电话</span>
			<div class="a_herf" @click="addMessageBoard">提 交</div>
		</div>

		<div class="liuyan_msg">
			<div class="title_msg">
				<span>精选留言</span>
			</div>
			<ul>
				<li v-if="messageList&&messageList.length>0" v-for="(list, index) in messageList">
					<!--<img :src="list.picUrl" />-->
					<div class="img_box bg_cover" :style="{backgroundImage: 'url('+list.picUrl+')'}">
						<img :src="list.picUrl" @load="successLoadImg" @error="errorLoadImg" />
					</div>
					<div class="text_div">
						<span>{{list.wechatName}}</span>
						<span>{{list.content}}</span>
						<span>{{list.createDate}}</span>
					</div>
				</li>
				<!--<li>
					<div class="img_box">
						<img src="../../../assets/image/detail01.jpg" />
					</div>
					<div class="text_div">
						<span>如果</span>
						<span>展览很好看</span>
						<span>昨天</span>
					</div>
				</li>-->
				<!--<li>
					<div class="img_box">
						<img src="../../../assets/image/detail01.jpg" />
					</div>
					<div class="text_div">
						<span>如果</span>
						<span>展览很好看</span>
						<span>昨天</span>
					</div>
				</li>-->
				<!--<li>
					<div class="img_box">
						<img src="../../../assets/image/detail01.jpg" />
					</div>
					<div class="text_div">
						<span>如果</span>
						<span>展览很好看</span>
						<span>昨天</span>
					</div>
				</li>-->
			</ul>
		</div>

	</div>
</template>

<script>
	import { Indicator, Toast } from 'mint-ui'
	//文本框校验正则
	let rejson = {
		contacts: /\S/,
		contactsPhone: /^([0-9]{3,4}-)?[0-9]{7,8}$/,
		content: /\S/
	};

	//手机号码校验
	let isMob = /^((\+?86)|(\+86))?(17[012356789][0-9]{8}|13[012356789][0-9]{8}|15[012356789][0-9]{8}|18[02356789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/;

	export default {
		data() {
			return {
				titleInfo: '韩美林',
				messageboard: {
					contacts: '',
					contactsPhone: '',
					content: '',
				},
				messageList: [],
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`

			}
		},
		created() { //只执行一次
			this.titleInfo = this.$route.meta.name;
		},
		components: { 
		},
		methods: {
			handleBlur(event) {
				let currentEl = event.target.name;
				//手机号码校验

				if(rejson[currentEl]) {
					if(currentEl == 'contactsPhone') {
						if(rejson[currentEl].test(event.target.value) || isMob.test(event.target.value)) {
							event.target.className = 'ok';
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							event.target.className = 'err';
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					} else {
						if(rejson[currentEl].test(event.target.value)) {
							event.target.className = 'ok';
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							event.target.className = 'err';
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					}

				}
			},

			//获取留言列表
			getMessageBoard() {
				this.ApiSever.service.getMessageBoard(null).then(res => {
					let result = res.data;
					if(result.success) {
						this.messageList = result.entitys;
					}
				});
			},

			//添加留言
			addMessageBoard() {
				let bOk = true;
				for(let name in rejson) {
					if(name == 'contactsPhone') {
						if(rejson[name].test(this.messageboard[name]) || isMob.test(this.messageboard[name])) {
							this.$refs[name].className = 'ok';
							this.$refs[name + '_span'].className = 'hiden';
						} else {
							this.$refs[name].className = 'err';
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						}
					} else {
						if(!rejson[name].test(this.messageboard[name])) {
							this.$refs[name].className = 'err';
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						} else {
							this.$refs[name].className = 'ok';
							this.$refs[name + '_span'].className = 'hiden';
						}
					}

				}

				if(!bOk) {
					return false;
				}

				let param = Object.assign({}, this.messageboard);
				console.log(param);

				this.ApiSever.service.addMessageBoard(param).then(res => {

					let result = res.data;
					if(result.success) {
						this.$toast('提交成功！');
						this.getMessageBoard();
						for(let name in rejson) {
							this.$refs[name].className = '';
						}
						this.messageboard.contacts = '';
						this.messageboard.contactsPhone = '';
						this.messageboard.content = '';
					}
				});
			}
		},
		mounted() {
			this.getMessageBoard();
		}
	}
</script>

<style type="text/css" scoped>
	.ok {
		border: 1px solid green !important;
	}
	
	.err {
		border: 1px solid red !important;
	}
	
	.errShow {
		width: 100%;
		height: 26px;
		display: block;
		color: red;
	}
	
	.hiden {
		display: none;
	}
</style>


