<template>
	<div id="documentary">
		<div class="content">
			<ul class="documentary-cont">
				<li v-if="listData&&listData.length>0" v-for="(list, index) in listData">
					<div class="div_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.imageUrl+')'}">
						<img :id="'imgBg_'+index" :src="imgURL+list.imageUrl" @load="successLoadImg" @error="errorLoadImg" />
						<img class="documentaryBtn" :data-index="index" src="../../../assets/image/videoBtn.png" @click="playVideo" />
						<video :id="'documentary_'+index" :src="videoUrl+list.videoUrl" width="100%" height="100%" controls="controls" style="display: none;">
							Your browser does not support the video tag.
						</video>
					</div>
					<h5>{{list.title}}</h5>
				</li>
				<li v-if="!listData||listData.length<1">
					<p>{{readyInfo}}</p>
				</li>
			</ul>
		</div>

	</div>
</template>

<script>
	import { Indicator } from 'mint-ui';

	export default {
		data() {
			return {
				readyInfo: '',
				listData: [],
				imgURL: this.ApiSever.defaultUrl,
				videoUrl: this.ApiSever.videoUrl
			}
		},
		created() {},
		components: {},
		methods: {
			/*
			 * 获取记录片数据
			 */
			getDocumentaryList() {
				let self = this;
				Indicator.open('加载中...');
				this.ApiSever.introduce.getDocumentary(null).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						self.listData = result.entitys;
						self.readyInfo = '暂无数据';
					}

				})
			},

			//播放视频事件
			playVideo(event) {
				this.stopVideo('documentary');
				let currentNum = event.currentTarget.dataset.index;
				let currentVideo = document.getElementById('documentary_' + currentNum);
				let currentImg = document.getElementById('imgBg_' + currentNum);
				let parent = event.target.parentNode;

				event.target.style.display = "none";
				parent.style.backgroundImage = '';
				currentImg.style.display = 'none';
				currentVideo.style.display = 'block';
				currentVideo.play();
			},
		},
		mounted() {
			this.getDocumentaryList();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


