<template>
	<div id="voiceMsg">

		<div v-if="detailData&&language=='chinese'">
			<div class="title_voice">
				<span class="span_left" @click="previousExhibition"><i class="iconfont">&#xe600;</i>上一展品</span>
				<!--<span class="span_active span_left" @click="previousExhibition"><i class="iconfont">&#xe600;</i>上一展品</span>-->
				<!--<span class="span_text">{{detailData.name}}</span>-->
				<span class="span_right" @click="nextExhibition">下一展品<i class="iconfont">&#xe601;</i></span>
			</div>
			<div class="detail-swipe" v-if="detailData.images&&detailData.images.length>0">
				<mt-swipe :auto="4000">
					<mt-swipe-item v-for="(list,index) in detailData.images" key="index">
						<!--<div class="detailBanner_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
						<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
					</div>-->
						<!--<img :src="imgURL+list.thumbPic" :onerror="imgError" />-->
						<a :href="imgURL+list.thumbPic+'?960x30000'">
							<div class="detailBanner_swipe">
								<img :src="imgURL+list.thumbPic" :onerror="imgError" />
							</div>
						</a>
					</mt-swipe-item>
				</mt-swipe>
			</div>
			<div class="voice_box">
				<audio id="Myaudio" v-if="detailData.guideVoicesEntity&&detailData.guideVoicesEntity.chineseVoice" :src="audioUrl+detailData.guideVoicesEntity.chineseVoice">
					您的浏览器不支持audio标签。
				</audio>
				<span class="btn prev_btn" @click="previousExhibition"><i class="iconfont">&#xe64d;</i></span>
				<span class="btn next_btn" @click="nextExhibition"><i class="iconfont">&#xe64e;</i></span>
			</div>
			<div class="voice_size">
			<span v-if="detailData.creationDate" class="librar_size">创作年代:{{detailData.creationDate}}</span>
			<span v-if="detailData.spec" class="librar_size">作品尺寸:{{detailData.spec}} </span>
			</div>
			<div v-if="detailData.descr" class="content" v-html="detailData.descr">
			</div>
		</div>
		<div v-if="detailData&&language=='English'">
			<div class="title_voice">
				<span class="span_active span_left" @click="previousExhibition"><i class="iconfont">&#xe600;</i>previous</span>
				<span class="span_text">{{detailData.nameEn}}</span>
				<span class="span_right" @click="nextExhibition">next<i class="iconfont">&#xe601;</i></span>
			</div>
			<div class="detail-swipe" v-if="detailData.images&&detailData.images.length>0">
				<mt-swipe :auto="4000">
					<mt-swipe-item v-for="(list,index) in detailData.images" key="index">
						<!--<div class="detailBanner_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
						<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
					</div>-->
						<!--<img :src="imgURL+list.thumbPic" :onerror="imgError" />-->
						<a :href="imgURL+list.thumbPic+'?960x30000'">
							<div class="detailBanner_swipe">
								<img :src="imgURL+list.thumbPic" :onerror="imgError" />
							</div>
						</a>
					</mt-swipe-item>
				</mt-swipe>
			</div>
			<div class="voice_box">
				<audio id="Myaudio" v-if="detailData.guideVoicesEntity&&detailData.guideVoicesEntity.englishVoice"  :src="audioUrl+detailData.guideVoicesEntity.englishVoice">
					您的浏览器不支持audio标签。
				</audio>
				<span class="btn prev_btn" @click="previousExhibition"><i class="iconfont">&#xe64d;</i></span>
				<span class="btn next_btn" @click="nextExhibition"><i class="iconfont">&#xe64e;</i></span>
			</div>
			<p v-if="detailData.creationDateEn" class="work_msg">Creating Date:{{detailData.creationDateEn}}</p>
			<p v-if="detailData.specEn" class="work_msg">Create specifications:{{detailData.specEn}} </p>
			<div v-if="detailData.descrEn" class="content" v-html="detailData.descrEn">
			</div>
		</div>
		<div class="content" v-if="!detailData">
			<p v-if="language=='chinese'">{{readyInfo}}</p>
			<p v-if="language=='English'">{{readyInfoEn}}</p>
		</div>
	</div>
</template>
<script>
	import { Indicator } from 'mint-ui';

	export default {
		data() {
			return {
				readyInfo: '',
				readyInfoEn: '',
				detailData: null,
				roomList: [],
				currentID: null, //当前展品的id
				language: this.$route.params.language,
				imgURL: this.ApiSever.httpUrl,
				audioUrl: this.ApiSever.audioUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		created() {},
		components: {},
		methods: {
			getVoiceMsg() {
				let self = this;
				let info = self.language == 'chinese' ? '加载中...' : 'loading...'
				Indicator.open(info);
				let param = {
					id: self.$route.params.id ? this.$route.params.id : null
				};

				self.ApiSever.visit.getVoiceMsg(param).then(res => {
					let result = res.data;
					if(result.success) {
						self.detailData = result.entity;
						self.currentID = result.entity.id;
						self.titleInfo = result.entity.name;
						//						window.document.title = result.entity.name;
						window.document.title = self.language == 'chinese' ? result.entity.name : result.entity.nameEn;
						self.readyInfo = '暂无数据';
						self.readyInfoEn = 'Transient data';
						setTimeout(() => {
							audiojs.events.ready(function() {
								Indicator.close();
								audiojs.createAll();
								$('html').one('touchstart', function() {
									self.handleAudioPlay();
								});
							});
						}, 200)
					}
				});
			},

			handleAudioPlay() {
				let aMyaudio = document.getElementById('Myaudio');
				if(!aMyaudio) return false;
				let firstPrent = aMyaudio.parentNode;
				aMyaudio.play();
				this.currentVudio = aMyaudio.src;
				this.januaryVudio = firstPrent.id;
				firstPrent.classList.add('playing');
			},

			//上一件展品点击事件
			previousExhibition() {
				let param = {
					roomId: this.detailData.guideVoicesEntity.exhibitionRoomId,
					itemId: this.detailData.id
				}

				this.ApiSever.visit.previousExhibition(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.detailData = result.entity;
						window.document.title = this.language == 'chinese' ? result.entity.name : result.entity.nameEn;

						let aMyaudio = document.getElementById('Myaudio');
						let firstPrent = aMyaudio.parentNode;
						aMyaudio.pause();
						firstPrent.classList.remove('playing');
						if(this.language == 'chinese') {
							aMyaudio.src = this.audioUrl + this.detailData.guideVoicesEntity.chineseVoice;
						} else if(this.language == 'English') {
							aMyaudio.src = this.audioUrl + this.detailData.guideVoicesEntity.englishVoice;
						}

					}
				});
			},

			//下一件展品点击事件
			nextExhibition() {
				let param = {
					roomId: this.detailData.guideVoicesEntity.exhibitionRoomId,
					itemId: this.detailData.id
				}

				this.ApiSever.visit.nextExhibition(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.detailData = result.entity;
						window.document.title = this.language == 'chinese' ? result.entity.name : result.entity.nameEn;

						let aMyaudio = document.getElementById('Myaudio');
						let firstPrent = aMyaudio.parentNode;
						aMyaudio.pause();
						firstPrent.classList.remove('playing');
						if(this.language == 'chinese') {
							aMyaudio.src = this.audioUrl + this.detailData.guideVoicesEntity.chineseVoice;
						} else if(this.language == 'English') {
							aMyaudio.src = this.audioUrl + this.detailData.guideVoicesEntity.englishVoice;
						}
					}
				});
			}

		},
		mounted() { //全部渲染完毕	
			this.getVoiceMsg();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css">

</style>


