<template>
	<!-- 跟节点 -->
	<div id="voiceAudio">
		<!--<mt-loadmore :top-method="loadTop" :bottom-all-loaded="allLoaded" @top-status-change="handleTopChange" @bottom-status-change="handleTopChange" ref="loadmore">-->

		<div class="div_user" v-if="roomInfo&&language=='chinese'" id="back_top">
			<img v-if="roomInfo" :src="imgURL+roomInfo.pic" :onerror="imgError">
			<div class="text_div">
				<div @click="getCurrntAudio">
					<audio id="Myaudio" v-if="roomInfo&&roomInfo.voiceZh" :src="audioUrl+roomInfo.voiceZh">
						您的浏览器不支持audio标签。
					</audio>
				</div>
				<div v-if="roomInfo.describe" class="dec" :class="{active:isShow}" v-html="roomInfo.describe">

				</div>
				<span v-if="roomInfo.describe" class="unfurled" @click="handleShow">{{showInfo}}</span>
				<p v-if="!roomInfo.describe">{{readyInfo}}</p>
			</div>
			<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
				<ul class="ul_abdio" v-if="listData&&listData.length>0">
					<li v-for="(list, index) in listData" key="index" @click="getCurrntAudio">
						<div class="div_vudio">
							<span v-if="list.name">{{list.name}}</span>
							<div class="div-vudio-cont">
								<audio v-if="list.guideVoicesEntity&&list.guideVoicesEntity.chineseVoice" :src="audioUrl+list.guideVoicesEntity.chineseVoice">
									您的浏览器不支持audio标签。
								</audio>
							</div>
						</div>
						<div class="project_box">
							<img :src="imgURL+list.thumbPic" @error="imgMinError" @click="handleJump(list.id)" />

						</div>
					</li>
				</ul>
			</mt-loadmore>

		</div>

		<div class="div_user" v-if="roomInfo&&language=='English'" id="back_top">
			<img v-if="roomInfo" :src="imgURL+roomInfo.pic" :onerror="imgError">
			<div class="text_div">
				<div @click="getCurrntAudio">
					<audio id="Myaudio" v-if="roomInfo&&roomInfo.voiceEn" :src="audioUrl+roomInfo.voiceEn">
						您的浏览器不支持audio标签。
					</audio>
				</div>
				<div v-if="roomInfo.describeEn" class="dec" :class="{active:isShow}" v-html="roomInfo.describeEn">
				</div>
				<span v-if="roomInfo.describeEn" class="unfurled" @click="handleShow">{{showInfoEn}}</span>
				<p v-if="!roomInfo.describeEn">{{readyInfoEn}}</p>
			</div>
			<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
				<ul class="ul_abdio" v-if="listData&&listData.length>0">
					<li v-for="(list, index) in listData" key="index" @click="getCurrntAudio">
						<div class="div_vudio">
							<span v-if="list.nameEn">{{list.nameEn}}</span>
							<div class="div-vudio-cont">
								<audio v-if="list.guideVoicesEntity&&list.guideVoicesEntity.englishVoice" :src="audioUrl+list.guideVoicesEntity.englishVoice">
									您的浏览器不支持audio标签。
								</audio>
							</div>
						</div>
						<div class="project_box">
							<img :src="imgURL+list.thumbPic" @error="imgMinError" @click="handleJump(list.id)" />

						</div>
					</li>
				</ul>
			</mt-loadmore>

		</div>

		<div v-if="!roomInfo">
			<p v-if="language=='chinese'">{{readyInfo}}</p>
			<p v-if="language=='English'">{{readyInfoEn}}</p>
		</div>

	</div>
</template>
<script>
	import { Indicator, Loadmore } from 'mint-ui';

	export default {
		data() {
			return {
				readyInfo: '',
				readyInfoEn: '',
				roomInfo: null,
				listData: [],
				isShow: false,
				showInfo: '展开',
				showInfoEn: 'show',
				playFlag: false,
				timer: null,
				currentVudio: null,
				januaryVudio: null,
				flag: true, //判断初始化界面后第一次触屏的是否是列表音频按钮，如果不是则默认播放第一个音频
				language: this.$route.params.language,
				imgURL: this.ApiSever.httpUrl,
				audioUrl: this.ApiSever.audioUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`,
				pageNo: 1,
				pageSize: 99999,
				allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
				scrollMode: "auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
				totalpage: 0,
				loading: false,
			}
		},
		created() { //只执行一次
			this.titleInfo = this.$route.meta.name;
		},
		components: {},
		methods: {
			//获取音频列表数据
			getVioceaudioList() {
				let self = this;
				let info = self.language == 'chinese' ? '加载中...' : 'loading...'
				Indicator.open(info);
				let param = {
					id: this.$route.params.id ? this.$route.params.id : null,
					pageNo: this.pageNo,
					pageSize: this.pageSize
				};

				self.ApiSever.visit.getVioceaudio(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						if(result.entitys == '暂无数据' || !result.entitys) {
							self.listData = [];
						} else {
							self.listData = result.entitys;
						}
						self.roomInfo = result.roomEntity;

						self.totalpage = result.count;
						self.titleInfo = result.roomEntity.name;
						self.readyInfo = '暂无数据';
						self.readyInfoEn = 'Transient data';
						window.document.title = self.language == 'chinese' ? result.roomEntity.name : result.roomEntity.nameEn;
						self.isHaveMore();
						setTimeout(() => {
							audiojs.events.ready(function() {
								audiojs.createAll();
								$('html').one('touchstart', function() {
									if(self.flag) {
										self.handleAudioPlay();
									}
								});
							});
						}, 500)

					}
				});
			},

			//获取正在播放的音频，纯原生js操作
			getCurrntAudio() {
				let currentEl = event.target;
				if(currentEl.classList.contains('play')) {
					this.flag = false;
					if(this.januaryVudio) {
						let oParent = document.getElementById(this.januaryVudio);
						let oAdiuo = oParent.getElementsByTagName('audio')[0];
						oAdiuo.pause();
						oParent.classList.remove('playing');
					}
					//获取'audiojs'元素  
					let currentParent = currentEl.parentNode.parentNode;
					let currentAudio = currentParent.getElementsByTagName('audio')[0];
					let currentId = currentParent.id;
					let currentSrc = currentAudio.src;
					this.januaryVudio = currentId;

					currentAudio.play();
					currentParent.classList.add('playing');
					this.currentVudio = currentSrc;
				}

			},

			//获取正在播放的音频,原生和jq混合使用
			getCurrntAudioBf() {

				if($(event.target).hasClass("play")) {
					this.flag = false;
					if(this.januaryVudio) {
						var oParentEl = document.getElementById(this.januaryVudio);
						var oAudio = oParentEl.getElementsByTagName('audio')[0];
						//						if(!oAudio.paused) {
						oAudio.pause();
						oParentEl.classList.remove('playing');
						//						}
					}
					let currentAudio = $(event.target).parents('.audiojs');

					let currentPlay = currentAudio.find('audio').attr('src');
					var oAudio = currentAudio.find('audio')[0];
					this.januaryVudio = currentAudio.attr('id');
					if(this.currentVudio == currentPlay) {
						this.currentVudio = null;
						return false;
					}
					currentAudio.attr('class', 'audiojs playing');
					oAudio.play();
					this.currentVudio = currentPlay;

				}
			},

			//展开和收起事件
			handleShow() {
				this.isShow = !this.isShow;
				if(this.isShow) {
					this.showInfoEn = 'hide';
					this.showInfo = '收起';
				} else {
					document.body.scrollTop = 0;
					this.showInfoEn = 'show';
					this.showInfo = '展开';
				}

			},

			//跳转到某个具体的界面
			handleJump(data) {

				this.$router.push({
					path: '/voiceMsg/' + data + '/' + this.language,
					params: {
						id: data,
						language: this.language
					}
				});
			},

			//默认播放音频事件  
			handleAudioPlay() {
				let aMyaudio = document.getElementById('Myaudio');
				if(!aMyaudio) return false;
				let firstPrent = aMyaudio.parentNode;
				aMyaudio.play();
				this.currentVudio = aMyaudio.src;
				this.januaryVudio = firstPrent.id;
				firstPrent.classList.add('playing');
			},

			loadBottom() {
				// 上拉加载
				this.toLoadmore(); // 上拉触发的分页查询
				this.$refs.loadmore.onBottomLoaded(); // 固定方法，查询完要调用一次，用于重新定位
			},

			toLoadmore() {
				// 分页查询
				if(this.totalpage == 1) {
					this.pageNo = 1;
					this.allLoaded = true;
				} else {
					this.pageNo = parseInt(this.pageNo) + 1;
					this.pageSize = parseInt(this.pageNo * this.pageSize);
					this.allLoaded = false;
				}
				this.getVioceaudioList();
			},
			isHaveMore() {
				this.allLoaded = false;
				let currentNum = parseInt(this.pageNo * this.pageSize);
				if(currentNum == this.totalpage || currentNum > this.totalpage) {
					this.allLoaded = true;
				}
			}
		},
		mounted() {
			this.getVioceaudioList();

		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css">

</style>


