// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import router from './router'
// 引入ajax注入原型
import Axios from 'axios'
import VueAxios from 'vue-axios'
import store from '@/store' // 引入vuex
//引入一个自定义的util函数，里面定义的是全局公用的方法
import utils from './components/component/utils';

import ApiSever from './api.js';

//移动端tap事件
import vueTap from 'v-tap';
Vue.use(vueTap)
Vue.use(MintUI);
Vue.use(utils);
Vue.use(ApiSever);

Vue.use(VueAxios, Axios) // 引入ajax
Vue.use(store) // 引入vuex
// Vue.config.productionTip = false

Vue.prototype.ApiSever = ApiSever;

Vue.prototype.successLoadImg = function(event) {
	if(event.target.complete == true) {
		var imgParentNode = event.target.parentNode;
		imgParentNode.classList.add("bg_cover");
		event.target.style.display = "none";
	}
};
Vue.prototype.errorLoadImg = function(event) {
	var imgParentNode = event.target.parentNode;
	imgParentNode.classList.remove("bg_cover");
	imgParentNode.classList.add('errorLoadImgbg_cover');
	event.target.style.display = "none";
};
Vue.prototype.errorMInLoadImg = function(event) {
	var imgParentNode = event.target.parentNode;
	imgParentNode.classList.remove("bg_cover");
	imgParentNode.classList.add('errorMinLoadImgbg_cover');
	event.target.style.display = "none";
};

Vue.prototype.actionPlay= function(Element) {
	let self = this;
	let oVideo = $('#' + Element + ' video');
	if(oVideo && oVideo.length > 0) {
		for(let i = 0, len = oVideo.length; i < len; i++) {
			(function(index) {
				self.creatImage(oVideo[index]);
			})(i);
		}
	}
};

Vue.prototype.stopVideo= function(Element) {

	let oVideo = $('#' + Element + ' video');
	if(oVideo && oVideo.length > 0) {
		for(let j = 0, len = oVideo.length; j < len; j++) {
			oVideo[j].pause();
		}
	}
};

Vue.prototype.creatImage= function(currentVideo) {
	let self = this;
	let parentEl = currentVideo.parentNode;
	let currentUrl = currentVideo.src;
	let frontCover = currentUrl.substr(currentUrl.indexOf('userfiles'), currentUrl.length)
	currentVideo.style.display = 'none';

	let creatDiv = document.createElement('div');
	creatDiv.className = 'richText';
	creatDiv.style.backgroundImage = HOST_CONFIG + 'video_thumbextractor/' + frontCover + '?second=1&width=1242&height=640';
	let oImage = new Image();
	oImage.className = 'videoBg'
	oImage.src = HOST_CONFIG + 'video_thumbextractor/' + frontCover + '?second=1&width=1242&height=640';
	creatDiv.appendChild(oImage);
	let oBtn = new Image();
	oBtn.className = 'videoBtn';
	oBtn.src = HOST_CONFIG + 'userfiles/defaultImg/videoBtn.png';
	creatDiv.appendChild(oBtn);
	parentEl.insertBefore(creatDiv, currentVideo);
	//				parentEl.appendChild(creatDiv);
	oBtn.onclick = function() {
		currentVideo.style.display = 'block';
		self.stopVideo('ongoingActive');
		setTimeout(() => {
			currentVideo.play();
			creatDiv.innerHTML = ''
			creatDiv.className = '';

		}, 10)

	}

};

Vue.prototype.goAnchor = function(selector) {

	let anchors = this.$el.querySelector(selector);
	if(anchors&&anchors.offsetTop) document.body.scrollTop = anchors.offsetTop;
};

new Vue({
	el: '#app',
	router,
	template: '<App/>',
	components: {
		App
	}
})


