<template>
	<div class="detai_page">
		<div class="content" v-if="listData">
			<h1 v-if="listData.title" class="title">{{listData.title}}</h1>
			<i v-if="listData.startDate" class="time">{{listData.startDate}}</i>
			<div v-if="listData&&listData.details" v-html="listData.details" id="publiceducaVideo">
			</div>
		</div>
		<div class="content" v-if="!listData||!listData.details"><p >{{readyInfo}}</p></div>
	</div>
</template>

<script>
	import { Indicator } from 'mint-ui'

	export default {
		data() {
			return {
				readyInfo: '',
				listData: null,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
			}
		},
		created() { //只执行一次
			this.titleInfo = this.$route.meta.name;
		},
		components: {
		},
		methods: {
			getPublicEducationDetail() {
				let self = this;
				Indicator.open('加载中...');
				let param = {
					id: this.$route.params.id ? this.$route.params.id : null
				};

				this.ApiSever.service.getPublicEducationDetail(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						this.listData = result.entity;
						window.document.title = result.entity.title?result.entity.title:'活动详情';
						self.readyInfo = '暂无数据';
						setTimeout(() => {
							self.actionPlay('publiceducaVideo');
						}, 100)
					}
				});
			}
		},

		mounted() { //全部渲染完毕
			this.getPublicEducationDetail();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


