<template>
	<div id="magnumOpus">
		<!--<div class="content">
			<p>{{detailList.remarks}}</p>
		</div>
		<div class="partition"></div>-->
		<div class="content" v-if="detailList&&detailList.describe" v-html="detailList.describe">
		</div>
		<div class="content" v-if="!detailList||!detailList.describe">
			<p>{{readyInfo}}</p>
		</div>
	</div>
</template>

<script>
	import { Indicator } from 'mint-ui';

	export default {
		data() {
			return {
				readyInfo: '',
				detailList: null,
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		created() {
		},
		components: { 
		},
		methods: {
			//获取设计作品欣赏详细信息以及作品欣赏列表
			getMagnumopusDetail() {
				let param = {
					id: this.$route.params.id ? this.$route.params.id : null
				};
				Indicator.open('加载中...');

				this.ApiSever.introduce.getMagnumopusDetail(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						this.detailList = result.entity;
						this.titleInfo = result.entity.classificationName;
						this.readyInfo='暂无数据';
						window.document.title = result.entity.classificationName;
					}
				})
			}

		},
		mounted() {
			this.getMagnumopusDetail();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


