<template>
	<!-- 跟节点 -->
	<div id="cultureProducts">

		<div class="cultureProducts-cont">
			<div class="slide-nav">
				<ul class="nav-all" v-if="productsList&&productsList.length>0">
					<li v-for="(list,index) in productsList" key="index" :class="{active:currentIndex==list.id}">
						<span @click="slideShow(list)" :class="{curenttype:currentProducts==list.id}">{{list.classificationName}}</span>
						<ul v-if="list.childerEntity&&list.childerEntity.length>0">
							<li v-for="(childer,item) in list.childerEntity" @click="getCultureProduct(childer.id)" :class="{curenttype:currentProducts==childer.id}">{{childer.classificationName}}</li>
						</ul>
					</li>
					<!--<li v-for="(list,index) in teteData" :class="{active:currentIndex==index}" @click="slideShow(index)">
						<span>{{list.name}}</span>
						<ul v-if="list.childer&&list.childer.length>0">
							<li v-for="(childer,item) in list.childer" @click="productsShow(childer.type)" :class="{curenttype:currentProducts==childer.type}">{{childer.name}}</li>
						</ul>
					</li>-->
				</ul>
			</div>
			<div class="products">
				<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
					<div class="products-list">
						<ul>
						</ul>
						<ul>
						</ul>
					</div>
				</mt-loadmore>
			</div>

		</div>

	</div>
</template>

<script>
	import { Loadmore, Indicator } from 'mint-ui';

	export default {
		data() {
			return {
				productsList: [], //分类数据
				currentIndex: null,
				currentProducts: null,
				topStatus: '',
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`,
				productData: {
					totalCount: 0, // 总条数
					pageNumber: 1, // 当前显示页号
					pageSize: 10, // 每页显示数据条数
					value: [], // 显示数据
				},
				allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
				scrollMode: "auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
				loading: false,
			}
		},
		created() { //只执行一次
		},
		components: {},
		methods: {

			//获取文创产品分类 
			getCultureTypelist() {
				let self = this;
				self.ApiSever.service.getCulturalType(null).then(res => {
					let result = res.data;
					if(result.success) {
						self.productsList = [...result.entity];
						let defualtId;
						if(self.productsList && self.productsList.length > 0) {
							self.currentIndex = self.productsList[0].id;
							defualtId = self.productsList[0].id;

							if(sessionStorage.type) {
								self.getCultureProduct(sessionStorage.type);
								if(sessionStorage.index) {
									let currentId = '#' + sessionStorage.index;
									setTimeout(() => {
										self.goAnchor(currentId);
									}, 500)

								}
								

							} else {
								self.getCultureProduct(defualtId);
							}
						}

					}
				});
			},

			//获取产品数据
			getCultureProduct(Id) {
				this.currentProducts = Id;

				let self = this;
				let param = {
					pageNumber: self.productData.pageNumber,
					pageSize: sessionStorage.size?sessionStorage.size:self.productData.pageSize,
					id: Id
				};
				Indicator.open('数据加载中...');
				self.ApiSever.service.getCultureProducts(param).then(res => {
					Indicator.close()
					let result = res.data;
					if(result.success) {
						self.productData.value = result.entitys;
						self.productData.totalCount = result.count;
						self.isHaveMore();
						self.appendLi();
					}
				});
			},

			//展示分类
			slideShow(data) {
				sessionStorage.type = data.id;
				this.currentIndex = data.id;
				sessionStorage.size=10;
				this.productData.pageSize = 10;
				this.getCultureProduct(data.id);
				//				if(!data.childerEntity || data.childerEntity.length < 1) {
				//					this.productData = [];
				//					this.appendLi();
				//					return false;
				//				} else {
				//					let defualtId = data.childerEntity[0].id;
				//					this.getCultureProduct(defualtId);
				//				}

			},

			//创建Li元素
			createLi(data) {
				let self = this;
				var oLi = document.createElement('li');
				var oDiv = document.createElement('div');
				oLi.id = 'current_' + data.id;
				var oImg = document.createElement('img');
				var oSpan = document.createElement('span');
				oDiv.className = "products-img";
				oImg.src = self.imgURL + data.thumbPic;

				oSpan.innerHTML = data.name;
				oImg.onerror = function() {
					oImg.src = self.imgURL + '/userfiles/defaultImg/defaultMin.jpg';
				}

				oDiv.appendChild(oImg);
				oLi.appendChild(oDiv);
				oLi.appendChild(oSpan);

				oLi.onclick = function() {
					sessionStorage.index = 'current_' + data.id;
					self.$router.push({
						path: '/cultureDetail/' + data.id,
						params: {
							id: data.id
						}
					});
				};

				return oLi;
			},

			appendLi() {
				let aUl = $('.products-list').find('ul');
				for(let i = 0, len = aUl.length; i < len; i++) {
					aUl[i].innerHTML = '';
				}

				let resultData = [...this.productData.value];
				if(!resultData || resultData.length < 1) {
					aUl[0].innerHTML = '<p>暂无数据</p>'
					return false;
				}

				for(let index in resultData) {
					let oLi = this.createLi(resultData[index]);
					//根据ul高度不同，往最小的里面插。
					let arr = [];
					for(let j = 0, len = aUl.length; j < len; j++) {
						arr.push(aUl[j]);
					}
					arr.sort(function(u1, u2) {
						return u1.scrollHeight - u2.scrollHeight;
					});
					arr[0].appendChild(oLi);
				}
			},

			loadBottom() {
				// 上拉加载
				this.toLoadmore(); // 上拉触发的分页查询
				this.$refs.loadmore.onBottomLoaded(); // 固定方法，查询完要调用一次，用于重新定位
			},

			toLoadmore() {
				// 分页查询
				if((this.productData.totalCount) / (this.productData.pageSize) <= 1) {
					this.productData.pageNumber = 1;
					this.allLoaded = true;
				} else {
					this.productData.pageSize = parseInt(this.productData.pageSize) + 10;
					sessionStorage.size=this.productData.pageSize;
					this.allLoaded = false;
				}
				this.getCultureProduct(this.currentProducts);
			},
			isHaveMore() {
				this.allLoaded = false;
				let currentNum = parseInt(this.productData.pageSize);
				if(currentNum == this.productData.totalCount || currentNum > this.productData.totalCount) {
					this.allLoaded = true;
				}
			}
		},
		mounted() { //全部渲染完毕
			this.getCultureTypelist();

		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


