import Vue from 'vue'
import VueRouter from 'vue-router'

import User from '@/components/introduce/user/user.vue'
import userDetail from '@/components/introduce/user/userDetail.vue'

//代表作
import MagnumOpus from '@/components/introduce/magnumopus/magnumOpus.vue'
import ArtAppreciation from '@/components/introduce/magnumopus/artAppreciation.vue'

//艺术馆简介
import ArtProfile from '@/components/introduce/artprofile/artProfile.vue'
import LibraryWork from '@/components/introduce/artprofile/libraryWork.vue'
import LibraryDetail from '@/components/introduce/artprofile/libraryDetail.vue'

//新闻资讯
import News from '@/components/introduce/news/news.vue'
import NewsDetail from '@/components/introduce/news/newsDetail.vue'

//纪录片
import Documentary from '@/components/introduce/documentary/documentary.vue'

//参观预约
import VisitAppointment from '@/components/service/visitappointment/visitAppointment.vue'
import Mymap from '@/components/service/visitappointment/map.vue'

//公众教育
import PublicEducation from '@/components/service/publiceducation/publicEducation.vue'
import EducationDetail from '@/components/service/publiceducation/educationDetail.vue'
import JoninPubliceducation from '@/components/service/publiceducation/joninPubliceducation.vue'

//文创产品
import CultureProducts from '@/components/service/cultureproducts/cultureProducts.vue'
import CultureDetail from '@/components/service/cultureproducts/cultureDetail.vue'

//留言板
import MessageBboard from '@/components/service/messageboard/messageBoard.vue'

//招聘
import Recruit from '@/components/service/recruit/recruit.vue'

//语音导览
import VoiceNav from '@/components/visit/audiotours/voiceNav.vue'
import VoiceAudio from '@/components/visit/audiotours/voiceAudio.vue' //
import VoiceMsg from '@/components/visit/audiotours/VoiceMsg.vue' //

//岩画欣赏
import RockPaintings from '@/components/visit/rockPaintings/rockPaintings.vue' 
import RockPaintingDetail from '@/components/visit/rockPaintings/rockPaintingDetail.vue' 

import VisitAppointmenthz from '@/components/service/visitappointment/hzvisitAppointment.vue'
import VisitAppointmentyc from '@/components/service/visitappointment/ycvisitAppointment.vue'
import HzMap from '@/components/service/visitappointment/hzmap.vue'

Vue.use(VueRouter)

let router = new VueRouter({
//	 mode: 'history',
	routes: [{
		path: '/',
		name: 'User',
		component: User,
		meta: {
			name: '韩美林简介'
		},
	}, {
		path: '/userDetail/:id?',
		name: 'userDetail',
		component: userDetail,
		meta: {
			name: '详情'
		},
	}, {
		path: '/magnumopus',
		component: MagnumOpus,
		name: 'MagnumOpus',
		meta: {
			name: '艺术分类代表作'
		}
	}, {
		path: '/artappreciation/:id?',
		component: ArtAppreciation,
		name: 'ArtAppreciation',
		meta: {
			name: '艺术分类代表作'
		}
	}, {
		path: '/artProfile',
		component: ArtProfile,
		name: 'ArtProfile',
		meta: {
			name: '北京韩美林艺术馆'
		}
	}, {
		path: '/libraryWork',
		component: LibraryWork,
		name: 'LibraryWork',
		meta: {
			name: '馆藏作品'
		}
	}, {
		path: '/libraryDetail/:id?',
		component: LibraryDetail,
		name: 'LibraryDetail',
		meta: {
			name: '馆藏作品详情'
		}
	}, {
		path: '/news',
		component: News,
		name: 'News',
		meta: {
			name: '新闻资讯'
		}
	}, {
		path: '/newsDetail/:id?',
		component: NewsDetail,
		name: 'NewsDetail',
		meta: {
			name: '新闻资讯详细'
		}
	}, {
		path: '/documentary',
		component: Documentary,
		name: 'Documentary',
		meta: {
			name: '纪录片'
		}
	}, {
		path: '/visitAppointment',
		component: VisitAppointment,
		name: 'VisitAppointment',
		meta: {
			name: '参观预约'
		}
	}, {
		path: '/map',
		component: Mymap,
		name: 'Mymap',
		meta: {
			name: '北京韩美林艺术馆'
		}
	}, {
		path: '/publicEducation',
		component: PublicEducation,
		name: 'PublicEducation',
		meta: {
			name: '公众教育'
		}
	}, {
		path: '/educationDetail/:id?',
		component: EducationDetail,
		name: 'EducationDetail',
		meta: {
			name: '公众教育详情'
		}
	}, {
		path: '/joninPubliceducation/:id?',
		component: JoninPubliceducation,
		name: 'JoninPubliceducation',
		meta: {
			name: '报名'
		}
	}, {
		path: '/culturecproducts',
		component: CultureProducts,
		name: 'CultureProducts',
		meta: {
			name: '文创产品',
			scrollToTop: true
		}
	}, {
		path: '/cultureDetail/:id?',
		component: CultureDetail,
		name: 'CultureDetail',
		meta: {
			name: '文创产品详情'
		}
	}, {
		path: '/messageboard',
		component: MessageBboard,
		name: 'MessageBboard',
		meta: {
			name: '留言板'
		}
	}, {
		path: '/recruit',
		component: Recruit,
		name: 'Recruit',
		meta: {
			name: '招聘'
		}
	}, {
		path: '/voiceNav',
		component: VoiceNav,
		name: 'VoiceNav',
		meta: {
			name: '语音导览'
		}
	}, {
		path: '/voiceAudio/:id?/:language?',
		component: VoiceAudio,
		name: 'voiceAudio',
		meta: {
			name: '语音导览'
		}
	}, {
		path: '/voiceMsg/:id?/:language?',
		component: VoiceMsg,
		name: 'voiceMsg',
		meta: {
			name: '语音导览'
		}
	}, {
		path: '/rockPaintings',
		component: RockPaintings,
		name: 'RockPaintings',
		meta: {
			name: '岩画赏析'
		}
	}, {
		path: '/rockPaintingDetail/:id?',
		component: RockPaintingDetail,
		name: 'RockPaintingDetail',
		meta: {
			name: '岩画赏析'
		}
	}, {
		path: '/visitAppointmentyc',
		component: VisitAppointmentyc,
		name: 'VisitAppointmentyc',
		meta: {
			name: '银川艺韩美林艺术馆'
		}
	}, {
		path: '/visitAppointmenthz',
		component: VisitAppointmenthz,
		name: 'VisitAppointmenthz',
		meta: {
			name: '杭州艺韩美林艺术馆'
		}
	}, {
		path: '/hzMap',
		component: HzMap,
		name: 'HzMap',
		meta: {
			name: '杭州艺韩美林艺术馆'
		}
	}]
})

router.afterEach((to, from, next) => {
	if(to.meta.name) {
		window.document.title = to.meta.name
	}
})
export default router


