<template>
	<div class="detai_page">

		<div class="content" v-if="detailData">
			<div>
				<h1 v-if="detailData.title" class="title">{{detailData.title}}</h1>
				<i class="time" v-if="detailData.year">{{detailData.year}}<em v-if="detailData.month">-</em></i>
				<i class="time" v-if="detailData.month">{{detailData.month}}<em v-if="detailData.day">-</em></i>
				<i class="time" v-if="detailData.day">-{{detailData.day}}</i>
				<div id="currentNew" v-if="detailData.context" v-html="detailData.context"></div>
				<p v-if="!detailData.context">{{readyInfo}}</p>
			</div>
			<div v-if="detailData.urls&&detailData.urls.length>0&&detailData.urls[0].name">
				<div class="title_msg">
					<span>相关新闻</span>
				</div>
				<!--<div class="related-news">
					<p v-for="(list, index) in detailData.urls">
						<a :href="list.url">{{list.name}}</a>
					</p>
					
				</div>-->
				<ul class="related-news" >
					<li v-for="(list, index) in detailData.urls" key="inde" v-if="list.name">
						<a v-if="list.url" :href="list.url">
							<div class="news">
								<div class="news-lf">
									<div class="title">
										{{list.name}}
									</div>
								</div>
								<div v-if="list.picUrl" class="news-rg bg_cover" :style="{backgroundImage: 'url('+imgURL+list.picUrl+')'}">
									<img :src="imgURL+list.picUrl" @load="successLoadImg" @error="errorLoadImg" />
								</div>
								<!--<div  v-if="!list.picUrl" class="news-rg errorMinLoadImgbg_cover" >
							<img src=""  />
						</div>-->
							</div>
						</a>
						<a v-if="!list.url" href="javascript:;">
							<div class="news">
								<div class="news-lf">
									<div class="title">
										{{list.name}}
									</div>
								</div>
								<div v-if="list.picUrl" class="news-rg bg_cover" :style="{backgroundImage: 'url('+imgURL+list.picUrl+')'}">
									<img :src="imgURL+list.picUrl" @load="successLoadImg" @error="errorLoadImg" />
								</div>
								<!--<div  v-if="!list.picUrl" class="news-rg errorMinLoadImgbg_cover" >
							<img src=""  />
						</div>-->
							</div>
						</a>
					</li>

				</ul>
			</div>

		</div>
		<div class="content" v-if="!detailData">
			<p>{{readyInfo}}</p>
		</div>

	</div>
</template>

<script>
	import { Indicator } from 'mint-ui';

	export default {
		data() {
			return {
				readyInfo: '',
				detailData: null,
				relatedNewData: [],
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		created() {
		},
		components: {},
		methods: {
			getUserInfoDetail() {
				let self = this;
				let param = {
					id: this.$route.params.id ? this.$route.params.id : null
				};

				this.ApiSever.introduce.getUserInfodetail(param).then(res => {
					let result = res.data;
					if(result.success) {
						self.detailData = result.entity;
						self.readyInfo = '暂无数据';
						window.document.title = result.entity.title ? result.entity.title : '详情';
					}
				});
			}
		},

		mounted() { //全部渲染完毕
			this.getUserInfoDetail();

		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


