import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'

Vue.prototype.$http = axios

// 常量 API 接口地址
const HOST = HOST_CONFIG + 'webservice/';

//图片服务器（静态资源）服务器地址

var CancelToken = axios.CancelToken;
var source = CancelToken.source();
const VueHttp = new Vue();
//用户名全局变量获取

/*获取近期公众教育数据*/
const ONPUBLIC_EDUCATION = `${HOST}activityManage/list`;

/*获取往期公众教育数据*/
const PASTPUBLIC_EDUCATION = `${HOST}activityManage/listLast`;

/*获取公众教育详情 id*/
const PUBLICEDUCATION_DETAIL = `${HOST}activityManage/getActivityManage`;

/*加入公众教育*/
const JONIN_PUBLICEDUCATION = `${HOST}activityEnroll/add`;

/*添加留言*/
const ADD_MESSAGEBOARD = `${HOST}question/add`;

/*获取语音导览*/
const VIOCENAV_LIST = `${HOST}exhibitionArea/getExhibitionAreaTree`;

/*获取语音播报列表*/
const VIOCEAUDIO_LIST = `${HOST}exhibitionRoom/list`;

/*获取语音详细*/
const VOICEMSG = `${HOST}exhibitionItem/getItem`;

/*上一展品接口 roomId是展厅id   itemId是展品id  这两个是必须字段*/
const PREVIOUS_EXHIBITION = `${HOST}exhibitionItem/findItemUp`;
/*下一展品接口 roomId是展厅id   itemId是展品id  这两个是必须字段*/
const NEXT_EXHIBITION = `${HOST}exhibitionItem/findItemDown`;

/*根据id查询展区并查询该展区下的展厅*/
const EXHIBITIONAREA = `${HOST}exhibitionArea/getExhibitionArea`;

/*获取大事记列表*/
const USER_INFO = `${HOST}hmlday/list`;

/*id获取大事记详情*/
const USERINFO_DETAIL = `${HOST}hmlday/getHmlDay`;

/*大事记相关链接*/
const FINDEVENTRELATED = `${HOST}hmlday/findEventRelated?code=eventRelated`;

/*所有资讯信息
 * 1(韩美林资讯)、2(三馆资讯 )、3(过往回顾未定)
 * pageNumer  1
 * pageSize   2
 * eg:information/list/1/1/2
 */
const NEWS_LIST = `${HOST}information/list`;

/*最新资讯轮播图接口*/
const NEWS_BANNER = `${HOST}information/banner`;

/*三馆接口*/
const RECOMMEND = `${HOST}information/recommend`;

/*通过Id获取新闻资讯详情 id */
const NEWS_DETAIL = `${HOST}information/get`;

/*馆藏作品列表*/
const LIBRARY_WORKS = `${HOST}opus/list`;

/*获取藏馆作品详情*/
const LIBRARY_DETAIL = `${HOST}opus/getOpus`;

/*设计作品欣赏*/
const MAGNUM_OPUS = `${HOST}classification/list?classtype=ysdbz`;

/*获取设计作品详情*/
const MAGNUMOPUS_DETAIL = `${HOST}classification/getClassification`;

/*获取文创产品左侧分类数据*/
const CULTUREPRODUCTS_TYPE = `${HOST}classification/getTrees?classtype=wccp`;

/*获取文创产品id获取具体文创产品*/
const CULTURE_PRODUCTS = `${HOST}culturalProduct/list`;

/*根据id获取文创产品详情*/
const CULTUREPRODUCTS_DETAIL = `${HOST}culturalProduct/getCulturalProduct`;

///*获取招聘头部信息*/
//const RECRUIT_INFO = `${HOST}voluntary/findContent`;

/*添加招聘信息*/
const ADD_RECRUIT = `${HOST}recruit/add`;

/*留言板*/
const MESSAGEBOARD_LIST = `${HOST}question/list`;

/*分类接口*/
const CLASSTYPE = `${HOST}classification/list`;

/*公众教育活动前言code=activityInfo，
艺术馆春秋招须知code=recruitInfoArt，
基金会春秋招须知code=recruitInfoFund，
基金会志愿者招聘须知code=voluntaryInfoFund
*/
const FINDCONTENT = `${HOST}voluntary/findContent`;

//纪录片接口
const GET_DOCUMENTARY = `${HOST}newsreel/list`;

export default {
	/*获取分类数据*/
	getClasstype: (jsons) => {
		if(jsons) {
			return VueHttp.$http.get(CLASSTYPE + '?classtype=' + jsons.type,
				qs.stringify(jsons)
			)
		} else {
			return VueHttp.$http.get(CLASSTYPE,
				qs.stringify(jsons)
			)
		}
	},

	introduce: {
		/*获取韩美林简介信息*/
		getUserInfo: (jsons) => {
			return VueHttp.$http.get(USER_INFO)
		},

		/*获取韩美林简介详细信息*/
		getUserInfodetail: (jsons) => {
			return VueHttp.$http.get(USERINFO_DETAIL + '?id=' + jsons.id)
		},
		/*大事记相关链接*/
		getFindeventrelated: (jsons) => {
			return VueHttp.$http.get(FINDEVENTRELATED)
		},

		/*最新资讯轮播图接口*/
		getnewsBanner: (jsons) => {
			return VueHttp.$http.get(NEWS_BANNER)
		},

		/*所有资讯信息
		 * 1(韩美林资讯)、2(三馆资讯 )、3(过往回顾未定)
		 * pageNumer  1
		 * pageSize   2
		 * eg:information/list/1/1/2
		 */
		getNews: (jsons) => {
			return VueHttp.$http.get(NEWS_LIST + '/' + jsons.type + '/' + jsons.pageNumber + '/' + jsons.pageSize)
		},

		/*三馆接口*/
		getRecommend: (jsons) => {
			return VueHttp.$http.get(RECOMMEND)
		},

		/*通过Id获取新闻资讯详情*/
		getNewsdetail: (jsons) => {
			return VueHttp.$http.get(NEWS_DETAIL+'/'+jsons.id)
		},

		/*获取艺术馆简介内容*/
		getArtproFile: (jsons) => {
			console.log('新闻资讯详情', jsons);
			return VueHttp.$http.get(ARTPRO_FILE,
				qs.stringify(jsons)
			)
		},

		/*获取馆藏作品*/
		getLibraryWork: (jsons) => {
			return VueHttp.$http.get(LIBRARY_WORKS + '?classification=gczp' + '&pageNo=' + jsons.pageNumber + '&pageSize=' + jsons.pageSize);
		},

		/*获取藏馆作品详情*/
		getLibraryDetail: (jsons) => {
			return VueHttp.$http.get(LIBRARY_DETAIL + '?id=' + jsons.id)
		},

		/*设计作品欣赏*/
		getMagnumOpus: (jsons) => {
			return VueHttp.$http.get(MAGNUM_OPUS + '&pageNo=' + jsons.pageNumber + '&pageSize=' + jsons.pageSize)
		},

		/*获取设计作品详情*/
		getMagnumopusDetail: (param) => {
			return VueHttp.$http.get(MAGNUMOPUS_DETAIL + '?id=' + param.id)
		},
		getDocumentary: (param) => {
			return VueHttp.$http.get(GET_DOCUMENTARY)
		}
	},
	service: {
		//获取文创产品左侧分类数据
		getCulturalType: (jsons) => {
			return VueHttp.$http.get(CULTUREPRODUCTS_TYPE)
		},

		/*获取文创产品列表*/
		getCultureProducts: (jsons) => {
			return VueHttp.$http.get(CULTURE_PRODUCTS + '?classification=' + jsons.id + '&pageNo=' + jsons.pageNumber + '&pageSize=' + jsons.pageSize)
		},

		/*获取文创产品详情*/
		getCultureProductsDetail: (jsons) => {
			return VueHttp.$http.get(CULTUREPRODUCTS_DETAIL + '?id=' + jsons.id)
		},

		//		getRecruitInfo: (jsons) => {
		//			return VueHttp.$http.get(RECRUIT_INFO)
		//
		//		},
		//招聘
		addRecruit: (jsons) => {
			return VueHttp.$http.post(ADD_RECRUIT,
				qs.stringify(jsons)
			)
		},

		//获取近期公众教育数据
		getOngoingData: (jsons) => {
			return VueHttp.$http.get(ONPUBLIC_EDUCATION + '?pageNo=1&pageSize=99999')
		},
		//获取往期公众教育数据
		getReviewPastData: (jsons) => {
			return VueHttp.$http.get(PASTPUBLIC_EDUCATION + '?pageNo=1&pageSize=99999')
		},

		/*获取公众教育详情*/
		getPublicEducationDetail: (jsons) => {
			return VueHttp.$http.get(PUBLICEDUCATION_DETAIL + '?id=' + jsons.id)
		},

		/*加入公众教育*/
		addpubliceducation: (jsons) => {
			return VueHttp.$http.post(JONIN_PUBLICEDUCATION,
				qs.stringify(jsons)
			)
		},

		/*留言板*/
		getMessageBoard: (jsons) => {
			return VueHttp.$http.get(MESSAGEBOARD_LIST)
		},

		/*添加留言*/
		addMessageBoard: (jsons) => {
			return VueHttp.$http.post(ADD_MESSAGEBOARD,
				qs.stringify(jsons)
			)
		},
		/*公众教育活动前言code=activityInfo，
艺术馆春秋招须知code=recruitInfoArt，
基金会春秋招须知code=recruitInfoFund，
基金会志愿者招聘须知code=voluntaryInfoFund
*/
		getFindcontent: (jsons) => {
			return VueHttp.$http.get(FINDCONTENT + '?code=' + jsons.code)
		},
	},
	visit: {
		//获取语音导览列表
		getVioceNav: (jsons) => {
			return VueHttp.$http.get(VIOCENAV_LIST)
		},
		/*根据id查询语音播报列表*/
		getVioceaudio: (jsons) => {
			return VueHttp.$http.get(VIOCEAUDIO_LIST + '?roomId=' + jsons.id + '&pageNo=' + jsons.pageNo + '&pageSize=' + jsons.pageSize)
		},

		/*获取语音详细*/
		getVoiceMsg: (jsons) => {
			return VueHttp.$http.get(VOICEMSG + '?id=' + jsons.id)
		},

		/*上一展品接口 roomId是展厅id   itemId是展品id  这两个是必须字段*/
		previousExhibition: (jsons) => {
			return VueHttp.$http.get(PREVIOUS_EXHIBITION + '?roomId=' + jsons.roomId + '&itemId=' + jsons.itemId)
		},
		/*下一展品接口 roomId是展厅id   itemId是展品id  这两个是必须字段*/
		nextExhibition: (jsons) => {
			return VueHttp.$http.get(NEXT_EXHIBITION + '?roomId=' + jsons.roomId + '&itemId=' + jsons.itemId)
		},

		/*岩画赏析接口*/
		getRockpaintings: (jsons) => {
			return VueHttp.$http.get(LIBRARY_WORKS + '?classification=yhsx' + '&pageNo=' + jsons.pageNumber + '&pageSize=' + jsons.pageSize);
		},

		/*获取岩画赏析详情*/
		getRockpaintingsDetail: (jsons) => {
			return VueHttp.$http.get(LIBRARY_DETAIL + '?id=' + jsons.id)
		},

	},
	uploaUurl: HOST_CONFIG ,
	httpUrl: DEFAULT_CONFIG + 'data',
	audioUrl: DEFAULT_CONFIG + 'audio',
	videoUrl: DEFAULT_CONFIG + 'video/',
	defaultUrl: DEFAULT_CONFIG
}


