<template>
	<div id="publiceducation">

		<div class="content">
			<div class="content" v-html="pageTitle"></div>
			<div class="publiceducation-title">
				<mt-navbar v-model="selected">
					<mt-tab-item id="ongoing">正在进行</mt-tab-item>
					<mt-tab-item id="reviewPast">往期回顾</mt-tab-item>
				</mt-navbar>
			</div>
		</div>
		<div class="publiceducation-cont">
			<mt-tab-container v-model="selected">
				<mt-tab-container-item id="ongoing">
					<div id="ongoingActive">
						<div v-if="ongoingList.value&&ongoingList.value.length>0" class="projects" v-for="(list,index) in ongoingList.value" key="index" :id="'ongoing_'+list.id">

							<h1>{{list.title}}</h1>

							<div class="box_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.picUrl+')'}">
								<img :src="imgURL+list.picUrl" @load="successLoadImg" @error="errorLoadImg" />
							</div>
							<!--<img :src="imgURL+list.picUrl" :onerror="imgError"/>-->
							<div :id="'projects_'+index" v-html="list.details" class="projects-dec" :class="{unfurled_active:list.id==currentId}">

							</div>
							<span class="show" @click="handleShow(list.id)" v-show="list.id!==currentId"><i >展开</i></span>
							<span class="show" @click="handleShow(list.id)" v-show="list.id==currentId"><i >收起</i></span>
							<div class="projects-span">
								<span v-if="list.recruitObject"><label>招募对象：</label>{{list.recruitObject}}</span>
								<span v-if="list.recruitPeople"><label>招募人数：</label>{{list.recruitPeople}}人</span>
								<span v-if="list.startDate"><label>活动时间：</label>{{list.startDate}}<i class="iconfont">&#xe6e0;</i>{{list.endDate}}</span>
							</div>

							<span :class="{'joninBtn2':index%2==0,'joninBtn1':index%2==1}" @click="registrationActivities(list.id)">报名活动</span>
						</div>
					</div>
					<div v-if="!ongoingList.value||ongoingList.value.length<1">
						<p>{{readyInfo}}</p>
					</div>
				</mt-tab-container-item>
				<mt-tab-container-item id="reviewPast">
					<div id="reviewpastActive">
						<div v-if="reviewPastList.value&&reviewPastList.value.length>0" class="projects" v-for="(list,index) in reviewPastList.value" key="index" :id="'reviewpastActive_'+list.id">

							<h1>{{list.title}}</h1>

							<div class="box_img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.picUrl+')'}">
								<img :src="imgURL+list.picUrl" @load="successLoadImg" @error="errorLoadImg" />
							</div>
							<div v-html="list.details" class="projects-dec"></div>
							<!--<span class="show" @click="handleShow(list.id)" v-show="list.id!==currentId"><i >展开</i></span>
							<span class="show" @click="handleShow(list.id)" v-show="list.id==currentId"><i >收起</i></span>
							<div class="projects-dec">
								<span v-if="list.recruitObject"><label>招募对象 ：</label>{{list.recruitObject}}</span>
								<span v-if="list.recruitPeople"><label>招募人数：</label>{{list.recruitPeople}}人</span>
								<span v-if="list.startDate"><label>活动时间：</label>{{list.startDate}}<i class="iconfont">&#xe6e0;</i>{{list.endDate}}</span>
							</div>-->
							<span :class="{'joninBtn1':index%2==0,'joninBtn2':index%2==1}" @click="getDetail(list.id)">查看活动</span>
						</div>
					</div>
					<div v-if="!reviewPastList.value||reviewPastList.value.length<1">
						<p>{{readyInfo}}</p>
					</div>
				</mt-tab-container-item>
			</mt-tab-container>
		</div>

	</div>
</template>

<script>
	import { Indicator } from 'mint-ui'
	import filter from '../../../filter.js'

	export default {
		data() {
			return {
				readyInfo: '',
				pageTitle: '',
				ongoingList: {
					pageNo: 1,
					pageSize: 10,
					value: []
				},
				reviewPastList: {
					pageNo: 1,
					pageSize: 10,
					value: []
				},
				imgUrl: this.ApiSever.httpUrl,
				selected: 'ongoing',
				currentId: null,
				showInfo: '展开',
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				firstNum: 0
			}
		},
		created() { //只执行一次
			if(sessionStorage.type){
				this.selected=sessionStorage.type;
			}
		},
		watch: {
			selected: function(val, oldVal) {
				sessionStorage.type=val;
				let self = this;
				this.firstNum += 1;
				if(this.firstNum < 2) {
					setTimeout(() => {
						self.actionPlay('reviewpastActive');
					}, 100);
				}

			}
		},
		components: {},
		methods: {
			getFindcontent() {
				let param = {
					code: 'activityInfo'
				};

				this.ApiSever.service.getFindcontent(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.pageTitle = result.content;
						
					}
				});
			},

			//获取近期公众教育数据
			getOngoingList() {
				let self = this;

				Indicator.open('加载中...');
				let param = {
					pageNo: this.ongoingList.pageNo,
					pageSize: this.ongoingList.pageSize,
				};

				this.ApiSever.service.getOngoingData(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						self.ongoingList.value = result.entitys;
						self.readyInfo = '暂无数据';
						setTimeout(() => {
							self.actionPlay('ongoingActive');
						}, 100)
					}
				});
			},

			//获取往期公众教育数据
			getreviewPastList() {
				Indicator.open('加载中...');
				let param = {
					pageNo: this.reviewPastList.pageNo,
					pageSize: this.reviewPastList.pageSize,
				};

				this.ApiSever.service.getReviewPastData(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						this.reviewPastList.value = result.entitys;
					}
				});
			},

			handleShow(id) {
				if(this.currentId == id){
					this.goAnchor('#ongoing_'+this.currentId);
				}
				this.currentId = this.currentId == id ? null : id;

			},

			//报名活动
			registrationActivities(data) {
				this.$router.push({
					path: '/joninPubliceducation/' + data,
					params: {
						id: data
					}
				});
			},
			getDetail(data) {
				this.$router.push({
					path: '/educationDetail/' + data,
					params: {
						id: data
					}
				});
			},

			//			actionPlay(Element) {
			//				let self = this;
			//				let oVideo = $('#' + Element + ' video');
			//				if(oVideo && oVideo.length > 0) {
			//					for(let i = 0, len = oVideo.length; i < len; i++) {
			//						(function(index) {
			//							self.creatImage(oVideo[index]);
			//						})(i);
			//					}
			//				}
			//			},
			//
			//			stopVideo(Element) {
			//				let oVideo = $('#' + Element + ' video');
			//				if(oVideo && oVideo.length > 0) {
			//					for(let j = 0, len = oVideo.length; j < len; j++) {
			//						oVideo[j].pause();
			//					}
			//				}
			//			},
			//
			//			creatImage(currentVideo) {
			//				let self = this;
			//				let parentEl = currentVideo.parentNode;
			//				let currentUrl = currentVideo.src;
			//				let frontCover = currentUrl.substr(currentUrl.indexOf('userfiles'), currentUrl.length)
			//				currentVideo.style.display = 'none';
			//
			//				let creatDiv = document.createElement('div');
			//				creatDiv.className = 'richText';
			//				creatDiv.style.backgroundImage = self.ApiSever.defaultUrl + 'video_thumbextractor/' + frontCover + '?second=1&width=1242&height=640';
			//				let oImage = new Image();
			//				oImage.className = 'videoBg'
			//				oImage.src = self.ApiSever.defaultUrl + 'video_thumbextractor/' + frontCover + '?second=1&width=1242&height=640';
			//				creatDiv.appendChild(oImage);
			//				let oBtn = new Image();
			//				oBtn.className = 'videoBtn';
			//				oBtn.src = self.ApiSever.defaultUrl + 'userfiles/defaultImg/videoBtn.png';
			//				creatDiv.appendChild(oBtn);
			//				parentEl.insertBefore(creatDiv, currentVideo);
			//				//				parentEl.appendChild(creatDiv);
			//				oBtn.onclick = function() {
			//					currentVideo.style.display = 'block';
			//					self.stopVideo('ongoingActive');
			//					setTimeout(() => {
			//						currentVideo.play();
			//						creatDiv.innerHTML = ''
			//						creatDiv.className = '';
			//
			//					}, 10)
			//
			//				}
			//
			//			}
		},
		mounted() { //全部渲染完毕
			this.getFindcontent();
			this.getOngoingList();
			this.getreviewPastList();

		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>

</style>


