<template>
	<div class="news_detais">
		<div v-if="detailList" class="detai_cont">
			<h1 class="title" v-if="detailList.title">{{detailList.title}}</h1>
			<i class="time" v-if="detailList.issueDate">{{detailList.issueDate}}</i>
			<div v-if="detailList.content" v-html="detailList.content"></div>
			<p v-if="!detailList.content">{{readyInfo}}</p>
		</div>

		<div v-if="!detailList" class="content">
			<p>{{readyInfo}}</p>
		</div>
	</div>
</template>

<script>
	import { Indicator, Toast } from 'mint-ui'

	export default {
		data() {
			return {
				readyInfo: '',
				detailList: null
			}
		},
		created() {},
		components: {},
		methods: {
			//获取新闻资讯列表
			getNewsdetail() {
				let param = {
					id: this.$route.params.id ? this.$route.params.id : null
				};

				Indicator.open('加载中...');
				this.ApiSever.introduce.getNewsdetail(param).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						this.detailList = result.entitys;
						this.readyInfo = '暂无数据';
						window.document.title = result.entitys.title;
					}
					if(!result.success) {
						Toast({
							message: '加载失败',
							duration: -1
						});
					}
				});
			},
		},
		mounted() {
						this.getNewsdetail();
		}
	}
</script>

<style type="text/css" scoped>

</style>


