<template>
	<div id="main">
		<div class="my-gallery" ref="mybox">
			<figure v-for="(list, index) in listData" key="index">
				<a :href="imgURL+list.thumbPic" :data-size="list.photoSize?list.photoSize:'827x413'">
					<div class="gallery-img bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
						<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
					</div>
				</a>
				<figcaption itemprop="caption description">{{list.title}}</figcaption>
			</figure>
		</div>
		<div class="pswp" tabindex="-1" role="dialog" aria-hidden="true">
			<div class="pswp__bg"></div>
			<div class="pswp__scroll-wrap">
				<div class="pswp__container">
					<div class="pswp__item"></div>
					<div class="pswp__item"></div>
					<div class="pswp__item"></div>
				</div>
				<div class="pswp__ui pswp__ui--hidden">

					<div class="pswp__top-bar">
						<div class="pswp__counter"></div>
						<button class="pswp__button pswp__button--close" title="Close (Esc)"></button>
						<button class="pswp__button pswp__button--share" title="Share"></button>
						<button class="pswp__button pswp__button--fs" title="Toggle fullscreen"></button>
						<button class="pswp__button pswp__button--zoom" title="Zoom in/out"></button>
						<div class="pswp__preloader">
							<div class="pswp__preloader__icn">
								<div class="pswp__preloader__cut">
									<div class="pswp__preloader__donut"></div>
								</div>
							</div>
						</div>
					</div>
					<div class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap">
						<div class="pswp__share-tooltip"></div>
					</div>
					<button class="pswp__button pswp__button--arrow--left" title="Previous (arrow left)"></button>
					<button class="pswp__button pswp__button--arrow--right" title="Next (arrow right)"></button>
					<div class="pswp__caption">
						<div class="pswp__caption__center"></div>
					</div>
				</div>
			</div>
		</div>

	</div>

</template>

<script>
	export default {
		data() {
			return {
				listData: this.data,
				isShow: false,
				imgURL: this.ApiSever.httpUrl,
			}
		},
		props: ['data'],
		components: { //引入组件

		},
		methods: {

		},
		created() { //只执行一次

		},
		mounted() {
			let num = Number(this.listData.length);
			let width = this.listData.length * 5.46875 + num + 'rem';

			$('.my-gallery').css({
				'width': width
			});
			initPhotoSwipeFromDOM('.my-gallery');
			let self = this;
			setTimeout(() => {

				let aHref = $('.my-gallery figure a');
				if(aHref && aHref.length > 0) {
					let conleData = Array.from(self.listData);

					for(let i = 0; i < aHref.length; i++) {
						let photoSize = getNaturalWidth(aHref[i]);

						conleData[i].photoSize = photoSize;

						self.listData = Array.from(conleData);
					}

				}
			}, 500)

		}
	}
</script>

<style scoped>
	/*必要样式*/
	
	#main {
		width: 100%;
		height: 5.46875rem;
		overflow-x: auto;
		margin-bottom: 10px;
		overflow-y: hidden;
	}
	
	.my-gallery {
		/*width: 800px;*/
		height: 5.46875rem;
		display: block;
		overflow-x: auto;
		overflow-y: hidden;
		margin-bottom: 0.256rem;
	}
	
	.my-gallery img {
		width: 100%;
		width: 100%;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		padding: 0 !important;
		max-height: 100%;
		max-width: 100%;
	}
	
	.gallery-img {
		width: 5.46875rem;
		height: 5.46875rem;
	}
	
	.my-gallery figure {
		display: block;
		float: left;
		margin: 0 5px 5px 0;
		position: relative;
		width: 5.46875rem;
		height: 5.46875rem;
		margin: 0.46875rem;
		/*width: 150px;*/
	}
	
	.my-gallery figcaption {
		display: none;
	}
</style>


