<template>
	<div id="recruit">

		<!--<div class="recruit-title">-->
			<!--<mt-navbar v-model="currentSelected">
				<!--<mt-tab-item id="personnel">招聘</mt-tab-item>-->
				<!--<mt-tab-item id="volunteers">志愿者招聘</mt-tab-item>-->
			<!--</mt-navbar>-->
		<!--</div>-->
		<!--<mt-tab-container v-model="currentSelected">
			<mt-tab-container-item id="personnel">-->
				<div class="recruit-detail">
					<!--<p>艺术馆在每年春、秋季进行招聘。具体招聘时间、岗位等详情请见韩美林艺术馆官方网站。</p>-->
					<div class="content" v-html="recruitInfo"></div>
					<form action="" class="recruit-form">
						<div><label>姓名</label>
							<input type="text" placeholder="请输入姓名" v-model="springAutumn.name" name="name" @blur="handleBlur" ref="name" />
							<span class="hiden" ref="name_span">请输入姓名</span>
						</div>
						<div><label>年龄</label>
							<input type="number" placeholder="请输入年龄" v-model="springAutumn.age" name="age" @blur="handleBlur" ref="age" />
							<span class="hiden" ref="age_span">请输入正确的年龄</span>
						</div>
						<div><label>性别</label>
							<input type="radio" name="sex" v-model="springAutumn.sex" value="1" /><i>男</i>
							<input type="radio" name="sex" v-model="springAutumn.sex" value="2" /><i>女</i>
						</div>
						<div><label>毕业学校</label>
							<input type="text" placeholder="请输入毕业学校" v-model="springAutumn.school" name="school" ref="school" />
							<span class="hiden" ref="school_span">请输入毕业学校</span>
						</div>
						<div><label>所学专业</label>
							<input type="text" placeholder="请输入所学专业" v-model="springAutumn.professional" name="professional" ref="professional" />
							<span class="hiden" ref="professional_span">请输入所学专业</span>
						</div>
						<div><label>特长</label>
							<input type="text" placeholder="请输入特长" v-model="springAutumn.specialty" name="specialty" ref="specialty" />
							<span class="hiden" ref="specialty_span">请输入特长</span>
						</div>
						<div><label>联系电话</label>
							<input type="tel" placeholder="请输入联系电话" v-model="springAutumn.tel" name="tel" @blur="handleBlur" ref="tel" />
							<span class="hiden" ref="tel_span">请输入联系电话</span>
						</div>
						<div><label>电子邮箱</label>
							<input type="email" placeholder="请输入电子邮箱" v-model="springAutumn.mail" name="mail" @blur="handleBlur" ref="mail" />
							<span class="hiden" ref="mail_span">请输入电子邮箱</span>
						</div>
						<div><label>备注</label>
							<textarea placeholder="请输入备注" v-model="springAutumn.remarks"></textarea>
						</div>
						<div>
							<label :class="{'vertical':springAutumn.thumbPic}">上传生活照</label>
							<!--<div class="input_div photo_div" :class="{'photo_div':thumbPic.length>0}">-->
							<div class="input_div photo_div">
								<!--<span class="change_photo" v-if="!thumbPic||thumbPic.length<3">
									<img src="../../../../dist/defaultImg/defaultMax.jpg"/>
								</span>-->

								<div class="input_photo bg_cover" v-if="thumbPic&&thumbPic.length>0" v-for="(list, index) in thumbPic" :style="{backgroundImage: 'url('+imgURL+list+')'}">
									<img :src="imgURL+list" @load="successLoadImg" @error="errorLoadImg" />
									<i class="close" @click="deletePhoto(index)">×</i>
								</div>
								<div class="input_photo bg_cover" v-if="!thumbPic||thumbPic.length<3">
									<img src="../../../assets/image/imgadd.png" ref="regPhoto" />
									<input v-if="!thumbPic||thumbPic.length<3" type="file" multiple="multiple" placeholder="请选择" name="thumbPic" ref="thumbPic" class="imgOne" @change="updataOne" />
									<span class="hiden" ref="thumbPic_span">请上传生活照</span>
								</div>
							</div>

						</div>
						<div>
							<label>下载简历</label>
							<div class="input_div">
								<div v-if="navigatorType=='iPhone'"></div>
								<a v-if="navigatorType=='iPhone'" :href="defaultUrl+'video/userfiles/1/pdf/zhaopin.pdf'">
									请下载简历模版
								</a>
								<a v-if="navigatorType!=='iPhone'" :href="defaultUrl+'data/doc/zhaopin.doc'">
									请下载简历模版
								</a>
							</div>

						</div>

					</form>

					<span class="a_herf" @click="submitData">提 交</span>
				</div>
			<!--</mt-tab-container-item>
		</mt-tab-container>-->
	</div>
</template>

<script>
	import { Navbar, TabItem, Field, Radio, Indicator, Toast, MessageBox } from 'mint-ui'

	//文本框校验正则
	let rejson = {
		name: /\S/,
		age: /^(?:[1-9][0-9]?|1[01][0-9]|120)$/,
		tel: /^([0-9]{3,4}-)?[0-9]{7,8}$/,
		mail: /^\w+@[a-zA-Z0-9\-]+(\.[a-zA-Z]{2,6}){1,2}$/,
		//		thumbPic: /\S/
	};

	//手机号码校验
	let isMob = /^((\+?86)|(\+86))?(17[012356789][0-9]{8}|13[012356789][0-9]{8}|15[012356789][0-9]{8}|18[02356789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/;

	export default {
		data() {
			return {
				titleInfo: '韩美林',
				currentSelected: 'personnel',
				recruitInfo: null,
				springAutumn: {
					name: '',
					age: '',
					sex: 1,
					school: '',
					professional: '',
					specialty: '',
					tel: '',
					mail: '',
					remarks: '',
					thumbPic: '',
					thumbPic2: '',
					thumbPic3: ''
				},
				thumbPic: [],
				imgURL: this.ApiSever.httpUrl,
				videoUrl: this.ApiSever.videoUrl,
				defaultUrl:this.ApiSever.defaultUrl,
				imgError: `this.src="${this.ApiSever.httpUrl}/userfiles/1/images/default.jpg"`,
				navigatorType: ''
			}
		},
		created() { //只执行一次
			this.titleInfo = this.$route.meta.name;
		},
		components: {
		},
		methods: {
			//获取招聘头部简介
			getRecruitInfoFund() {
				let param = {
					code: 'recruitInfoArt'
				};

				this.ApiSever.service.getFindcontent(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.recruitInfo = result.content;
					}
				});

			},

			handleBlur(event) {
				let currentEl = event.target.name;
				if(rejson[currentEl]) {
					if(currentEl == 'tel') {
						if(rejson[currentEl].test(event.target.value) || isMob.test(event.target.value)) {
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					} else {
						if(rejson[currentEl].test(event.target.value)) {
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					}
				}
			},

			//点击提交按钮事件
			submitData() {
				let bOk = true;
				for(let name in rejson) {
					if(name == 'tel') {
						if(rejson[name].test(this.springAutumn[name]) || isMob.test(this.springAutumn[name])) {
							this.$refs[name + '_span'].className = 'hiden';
						} else {
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						}
					} else {
						if(!rejson[name].test(this.springAutumn[name])) {
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						} else {
							this.$refs[name + '_span'].className = 'hiden';
						}
					}

				}
				//				if(this.thumbPic.length < 1) {
				//					//					console.log(this.$refs['regPhoto'].className);
				//					//					this.$refs['regPhoto'].className = 'error_photo';
				//					this.$refs['regPhoto'].classList.add('error_photo');
				//					bOk = false;
				//				} else {
				//					this.$refs['regPhoto'].classList.remove('error_photo');
				//					//					this.$refs['regPhoto'].className = 'error_photo';
				//				}
				let resultPhoto = [...this.thumbPic];
				if(resultPhoto.length < 1) {
					this.$refs['regPhoto'].classList.add('error_photo');
					bOk = false;
				} else {
					if(this.$refs['regPhoto'] && this.$refs['regPhoto'].classList.contains('error_photo')) {
						this.$refs['regPhoto'].classList.remove('error_photo');
					}
					if(resultPhoto.length == 1) {
						this.springAutumn.thumbPic = resultPhoto[0];
					} else if(resultPhoto.length == 2) {
						this.springAutumn.thumbPic = resultPhoto[0];
						this.springAutumn.thumbPic2 = resultPhoto[1];

					} else if(resultPhoto.length == 3) {
						this.springAutumn.thumbPic = resultPhoto[0];
						this.springAutumn.thumbPic2 = resultPhoto[1];
						this.springAutumn.thumbPic3 = resultPhoto[2];
					}
				}

				if(!bOk) {
					return false;
				}
				let param = Object.assign({}, this.springAutumn);
				//				param.thumbPic = JSON.stringify(this.thumbPic);

				this.ApiSever.service.addRecruit(param).then(res => {
					let result = res.data;

					if(result.success) {
						this.$toast({
							message: '提交成功！',
							iconClass: 'icon icon-success'
						});
						this.springAutumn.name = '';
						this.springAutumn.age = '';
						this.springAutumn.sex = 1;
						this.springAutumn.school = '';
						this.springAutumn.professional = '';
						this.springAutumn.specialty = '';
						this.springAutumn.tel = '';
						this.springAutumn.mail = '';
						this.springAutumn.remarks = '';
						this.springAutumn.thumbPic = '';
						this.springAutumn.thumbPic2 = '';
						this.springAutumn.thumbPic3 = '';
						this.thumbPic = [];
					} else {
						switch(result.message) {
							case -1:
								MessageBox.alert('您的简历已收到，一个月内不可以重复提交哦！');
								break;
							case -2:
								MessageBox.alert('您的简历已收到，一个月内不可以重复提交哦！');
								break;
							case -3:
								MessageBox.alert('您还没授权！');
								break;
							case -4:
								MessageBox.alert('程序查询内部错误！');
								break;
							default:
								break;
						}
					}

				});

			},

			updataOne() {
				var vm = this;
				if($('.imgOne').get(0).files[0].type.indexOf("image") != -1) {
					Indicator.open('上传中...');
					var fd = new FileReader();
					fd.readAsDataURL($('.imgOne').get(0).files[0]);
					fd.onload = function() {
						if(fd.result) {
							var formData = new FormData();

							formData.append("file", $('.imgOne').get(0).files[0]);
							$.ajax({
								//								url: vm.ApiSever.uploaUurl + '/webservice/voluntary/upFile',
								url: vm.ApiSever.uploaUurl + '/webservice/file/upFile',
								type: 'POST',
								data: formData,
								processData: false,
								contentType: false,
								success: function(responseStr) {
									var result = responseStr;
									Indicator.close();
									if(result.success) {
										vm.$toast('上传照片成功！');
										$('.imgOne').val("");
										vm.thumbPic.push(result.imagePath);

										if(vm.$refs['regPhoto'] && vm.$refs['regPhoto'].classList.contains('error_photo')) {
											vm.$refs['regPhoto'].classList.remove('error_photo');

										}
									} else {
										$('.imgOne').val("");
										alert('上传失败')
									}
								},
								error: function(responseStr) {
									alert('上传失败')
									Indicator.close()
								}
							});
						}
					}
				} else {
					alert('上传图片不符合规则，请重新上传');
				}
			},

			deletePhoto(index) {
				let photoData = [...this.thumbPic];
				let result = photoData.splice(index, 1);
				this.thumbPic = [...photoData];
			}
		},
		mounted() {
			let self = this;
			this.getRecruitInfoFund();
			let u = navigator.userAgent;
			if(u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) { //安卓手机
				self.navigatorType = 'Android';
			} else if(u.indexOf('iPhone') > -1) { //苹果手机
				self.navigatorType = 'iPhone';
			} else if(u.indexOf('Windows Phone') > -1) { //winphone手机
				self.navigatorType = 'iPhone';
			}
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>
	.mint-tab-container {
		background: #fff;
	}
	
	.promptData {
		width: 100%;
		padding-left: 5rem;
		box-sizing: border-box;
		display: block;
		color: red;
		clear: both;
	}
	
	.input_div .errShow {
		padding-left: 0 !important;
	}
	
	.errShow {
		width: 100%;
		height: 0.4375rem;
		line-height: 0.4375rem;
		display: block;
		color: red;
		padding-left: 5rem;
		box-sizing: border-box;
	}
	
	.hiden {
		display: none;
	}
	
	.change_photo {
		width: 100%;
		height: 1.2rem !important;
		display: block;
	}
	
	.input_photo {
		position: relative;
		margin-top: 0.2rem;
		margin-left: 0.6rem;
		width: 2.6rem;
		height: 2.6rem;
		float: left;
	}
	
	.input_photo .close {
		position: absolute;
		top: 0;
		right: 0;
		margin-top: -0.468rem;
		margin-right: -0.468rem;
		width: 0.9375rem;
		height: 0.9375rem;
		line-height: 0.9375rem;
		background: #fff;
		border-radius: 50%;
		border: 2px solid red;
		color: #666;
		text-align: center;
		font-size: 1rem !important;
		font-weight: 600;
	}
	
	.input_photo img {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		max-height: 100%;
		max-width: 100%;
	}
	
	.vertical {
		vertical-align: top;
	}
	
	.photo_div {
		height: 3rem !important;
	}
	
	.imgOne {
		position: absolute;
		top: 0;
		opacity: 0;
		width: 2.6rem;
		height: 2.6rem;
		overflow: hidden;
	}
	
	.error_photo {
		border: 1px solid red;
	}
</style>


